<template>
  <div>
    <div class="row">
      <div class="col-md-12">
        <h3>Evento: {{ evento.type }}</h3>
        <h3>Data: {{ evento.dataEvento.toLocaleDateString() }}</h3>
      </div>
    </div>
    <hr>
    <!--
    <div class="row">
      <div class="col-md-4">
        <div class="card bg-gradient-primary card-img-holder text-white">
          <div class="card-body">
            <img src="../../assets/images/dashboard/circle.svg" class="card-img-absolute" alt="circle-image"/>
            <h4 class="font-weight-normal mb-2">Capital Social
              <i class="mdi mdi-coin mdi-48px float-right"></i>
            </h4>
            <h3 class="mb-2">R$ {{ capitalSocialTotal }}</h3>
          </div>
        </div>
      </div>
      <div class="col-md-8">
        <b-progress height="3rem" :max="capitalSocialTotal" show-value class="my-3">
          <b-progress-bar variant="primary" :value="capitalSocial.valorIntegralizado"></b-progress-bar>
          <b-progress-bar variant="primary" :value="capitalSocial.valorIntegralizar" striped style="opacity: 0.75"></b-progress-bar>
          <b-progress-bar variant="warning" :value="capitalSocial.reservaIntegralizada"></b-progress-bar>
          <b-progress-bar variant="warning" :value="capitalSocial.reservaIntegralizar" striped style="opacity: 0.75"></b-progress-bar>
        </b-progress>
      </div>
      <div class="col-md-12">
        <h3>Capital Social</h3>
        <table class="table table-striped">
          <tbody>
            <tr>
              <th>Capital social integralizado</th>
              <td class="text-right">{{ capitalSocial.valorIntegralizado }}</td>
            </tr>
            <tr>
              <th>Capital social a integralizar</th>
              <td class="text-right">{{ capitalSocial.valorIntegralizar }}</td>
            </tr>
            <tr>
              <th>Reserva integralizada</th>
              <td class="text-right">{{ capitalSocial.reservaIntegralizada }}</td>
            </tr>
            <tr>
              <th>Reserva a integralizar</th>
              <td class="text-right">{{ capitalSocial.reservaIntegralizar }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <th>TOTAL:</th>
              <th class="text-right">{{ capitalSocialTotal }}</th>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <hr>
    -->

    <div class="row">
      <div class="col-md-12">
        <h3>Composição acionária e capital social após evento</h3>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Nome do sócio/acionista</th>
              <th>Tipo</th>
              <th v-for="especieClasse in especiesClasses" :key="especieClasse">{{ especieClasse }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="socio in evento.socios" :key="socio.id">
              <td>{{ socio.nome }}</td>
              <td>{{ socio.tipo }}</td>
              <td v-for="especieClasse in especiesClasses" :key="especieClasse">
                {{ socio.acoes[especieClasse] ? socio.acoes[especieClasse].qtd : 0 }}
              </td>
              <!--
              <template v-for="especie in especiesClasses">
                <td style="text-align: right" :key="especie + '_qtd_' + pessoa.id">{{ pessoa.valores[especie] ? pessoa.valores[especie].qtd : 0 }}</td>
                <td style="text-align: right" :key="especie + '_val' + pessoa.id">{{ pessoa.valores[especie] ? pessoa.valores[especie].preco : 0 }}</td>
              </template>
              -->
              <!--
              <td>
                <table class="table table-striped table-sm">
                  <thead class="thead-dark">
                    <tr>
                      <th>Espécie/classe</th>
                      <th>Quantidade</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(val, key) in pessoa.valores" :key="key">
                      <td>{{ key }}</td>
                      <td>{{ val }}</td>
                    </tr>
                  </tbody>
                </table>
              </td>
              -->
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2">TOTAL</td>
              <td v-for="especieClasse in especiesClasses" :key="especieClasse">
                {{ valorTotal[especieClasse].qtd }}
              </td>
              <!--
              <template v-for="especie in especiesClasses">
                <td style="text-align: right" :key="especie">{{ valorTotal[especie] ? valorTotal[especie].qtd : 0 }}</td>
                <td style="text-align: right" :key="especie">{{ valorTotal[especie] ? valorTotal[especie].preco : 0 }}</td>
              </template>
              -->
              <!--
              <td>
                <table class="table table-striped table-sm">
                  <thead class="thead-dark">
                    <tr>
                      <th>Espécie/classe</th>
                      <th>Quantidade</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(val, key) in valorTotal" :key="key">
                      <td>{{ key }}</td>
                      <td>{{ val }}</td>
                    </tr>
                  </tbody>
                </table>
              </td>
              -->
            </tr>
          </tfoot>
        </table>
      </div>

    </div>
    <hr>
  </div>
</template>
<script>
export default {
  props: ['id', 'eventoIdx'],
  data () {
    return {
      especiesClasses: [],
      evento: {}
    }
  },
  computed: {
    valorTotal () {
      let valorTotal = {}
      for (let especieClasse of this.especiesClasses) {
        valorTotal[especieClasse] = {
          qtd: 0
        }
      }
      for (let socio of this.evento.socios) {
        for (let especieClasse of this.especiesClasses) {
          if (socio.acoes[especieClasse]) {
            valorTotal[especieClasse].qtd += socio.acoes[especieClasse].qtd
          }
        }
      }
      console.log('Valor total: ' + JSON.stringify(valorTotal))
      return valorTotal
    }
  },
  beforeMount () {

  },
  mounted () {
    this.updateData()
  },
  beforeUpdate () {
    this.updateData()
  },
  methods: {
    updateData () {
      console.log('EventoIdx: ' + this.eventoIdx)
      this.evento = this.$store.getters.eventos[this.eventoIdx]
      console.log('Society Summary: ' + JSON.stringify(this.evento))
      let especiesClasses = []
      for (let socio of this.evento.socios) {
        for (let especieClasse in socio.acoes) {
          if (!especiesClasses.includes(especieClasse)) {
            especiesClasses.push(especieClasse)
          }
        }
      }
      this.especiesClasses = especiesClasses
    }
  }
}
</script>



