<template>
  <event-base :societyName="sociedade.nome" :eventType="'Transferência'">
    <div class="row" v-if="step === 1">
      <div class="col-md-12">
        <b-form-group label-cols-sm="2" label="Data" label-for="dataEvento">
          <b-form-input type="date" id="dataEvento" v-model="dataEvento" />
        </b-form-group>
      </div>
    </div>

    <!-- ********** ETAPA 1: SELECIONAR VENDEDORES E ESPECIE DE AÇÕES A SEREM TRANSFERIDAS ********** -->
    <div class="row" v-if="step === 1">
      <div class="col-sm-12 col-md-6">
        <h4>Selecione os vendedores</h4>
        <table class="table table-striped table-sm">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Nome</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(socio, socioIdx) in socios" :key="socioIdx">
              <td>
                <b-form-checkbox size="lg" v-model="socio.vendedor" />
              </td>
              <td>{{ socio.nome }}</td>
            </tr>
          </tbody>
        </table>
        <!-- <p>{{ JSON.stringify(socios) }}</p> -->
      </div>
      <div class="col-sm-12 col-md-6">
        <h4>Selecione os compradores</h4>
        <table class="table table-striped table-sm">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Nome</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(socio, socioIdx) in socios" :key="socioIdx">
              <td>
                <b-form-checkbox size="lg" v-model="socio.comprador" />
              </td>
              <td>{{ socio.nome }}</td>
            </tr>
          </tbody>
        </table>
        <b-button class="btn btn-primary" ref="novoSocio" @click="openModalNovoSocio">Adicionar novo sócio/acionista</b-button>
        <!-- <p>{{ JSON.stringify(socios) }}</p> -->
      </div>
      <div class="col-sm-12 col-md-6">
        <h4>Selecione as espécies das ações</h4>
        <table class="table table-striped table-sm">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Espécie/Classe</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="especieClasse in especiesClasses" :key="especieClasse">
              <td>
                <b-form-checkbox size="lg" v-model="especiesClassesSelecionadas" :value="especieClasse" />
              </td>
              <td>{{ especieClasse }}</td>
            </tr>
          </tbody>
        </table>
        <!-- <p>{{ JSON.stringify(especiesClassesSelecionadas) }}</p> -->
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" :to="{ name: 'Society' }">Cancelar</b-button>
        <b-button class="btn btn-gradient-primary" @click="goStep2">Preencher valores</b-button>
      </div>
    </div>

    <!-- ********** ETAPA 2: PREENCHE OS VALORES REFERENTES A VENDA ********** -->
    <div class="row" v-if="step === 2">
      <div class="col-12">
        <h4>Ações a serem transferidas</h4>
        <template v-for="(socioVend, socioVendIdx) in socios">
          <div class="card my-3" v-if="socioVend.vendedor" :key="socioVendIdx">
            <div class="card-header bg-gradient-primary">
              Vendedor: {{ socioVend.nome }}
            </div>
            <div class="card-body py-1">
              <div class="row">
                <div class="col-12" style="overflow-x: auto; overflow-y: auto">
                  <table class="table table-striped step2">
                    <thead>
                      <tr>
                        <th>Compradores</th>
                        <th v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                          {{ especieClasse }}<br>(Disponíveis: {{ socioVend.acoes[especieClasse].qtd }})
                        </th>
                        <th>Valor total</th>
                        <th>Valor pago</th>
                      </tr>
                    </thead>
                    <tbody>
                      <template v-for="(socioCompr, socioComprIdx) in socios">
                        <tr v-if="socioCompr.comprador && socioComprIdx != socioVendIdx" :key="socioVendIdx + '_' + socioComprIdx">
                          <th>{{ socioCompr.nome }}</th>
                          <td v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                            <b-form-group :description="'Qtde atual: ' + socioCompr.acoes[especieClasse].qtd">
                              <b-form-input type="number" size="sm" number :disabled="socioVend.acoes[especieClasse].qtd < 1" v-model="socioCompr.transacao[socioVendIdx].acoes[especieClasse].qtd" />
                            </b-form-group>
                          </td>
                          <td>
                            <b-form-group>
                              <b-form-input type="number" size="sm" number lazy v-model="socioCompr.transacao[socioVendIdx].valorTotal" />
                            </b-form-group>
                          </td>
                          <td>
                            <b-form-group :description="'Valor pendente: ' + calculateValorPendente(socioCompr.transacao[socioVendIdx].valorTotal, socioCompr.transacao[socioVendIdx].valorPago)">
                              <b-form-input type="number" size="sm" number lazy v-model="socioCompr.transacao[socioVendIdx].valorPago" />
                            </b-form-group>
                          </td>
                        </tr>
                      </template>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" @click="goStep1">Voltar</b-button>
        <b-button class="btn btn-gradient-primary" @click="goStep3">Revisar valores</b-button>
      </div>
    </div>
    <!-- ********** ETAPA 3: PREENCHE OS VALORES REFERENTES A VENDA ********** -->
    <div class="row" v-if="step === 3">
      <div class="col-12">
        <h4>Ações a serem transferidas</h4>
        <template v-for="(socioVend, socioVendIdx) in socios">
          <div class="card my-3" v-if="socioVend.vendedor" :key="socioVendIdx">
            <div class="card-header bg-gradient-primary">
              <div class="row">
                <div class="col-12">
                  Vendedor: {{ socioVend.nome }}
                </div>
              </div>
            </div>
            <div class="card-body py-1">
              <div class="row mb-3">
                <div class="col-8" style="overflow-x: auto; overflow-y: auto">
                  <table class="table table-striped step3">
                    <thead>
                      <tr>
                        <th>Ações vendidas</th>
                        <template v-for="especieClasse in especiesClassesSelecionadas">
                          <th :key="especieClasse">
                            {{ especieClasse }}
                          </th>
                        </template>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Atual</td>
                        <template v-for="especieClasse in especiesClassesSelecionadas">
                          <td :key="especieClasse">
                            {{ socioVend.acoes[especieClasse].qtd }}
                          </td>
                        </template>
                      </tr>
                      <tr>
                        <td>Vendidas</td>
                        <template v-for="especieClasse in especiesClassesSelecionadas">
                          <td :key="especieClasse">
                            <b>{{ valoresVendedor[socioVendIdx].acoes[especieClasse].qtd }}</b>
                          </td>
                        </template>
                      </tr>
                      <tr>
                        <td>Final</td>
                        <template v-for="especieClasse in especiesClassesSelecionadas">
                          <td :key="especieClasse">
                            {{ socioVend.acoes[especieClasse].qtd - valoresVendedor[socioVendIdx].acoes[especieClasse].qtd }}
                          </td>
                        </template>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div class="col-4" style="overflow-x: auto; overflow-y: auto">
                  <ul class="list-group">
                    <li class="list-group-item">Valor recebido: {{ valoresVendedor[socioVendIdx].pago }}</li>
                    <li class="list-group-item">Valor a receber: {{ valoresVendedor[socioVendIdx].pendente }}</li>
                  </ul>
                </div>
                <hr />
                <div class="col-12" style="overflow-x: auto; overflow-y: auto">
                  <table class="table table-striped step3">
                    <thead>
                      <tr>
                        <th>Compradores</th>
                        <th v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                          {{ especieClasse }}<br>(Disponíveis: {{ socioVend.acoes[especieClasse].qtd }})
                        </th>
                        <th>Valor total</th>
                        <th>Valor pago</th>
                        <th>Valor a pagar</th>
                      </tr>
                    </thead>
                    <tbody>
                      <template v-for="(socioCompr, socioComprIdx) in socios">
                        <tr v-if="socioCompr.comprador && socioComprIdx != socioVendIdx" :key="socioVendIdx + '_' + socioComprIdx">
                          <th>{{ socioCompr.nome }}</th>
                          <td v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                            {{ socioCompr.transacao[socioVendIdx].acoes[especieClasse].qtd }}
                          </td>
                          <td>
                            {{ socioCompr.transacao[socioVendIdx].valorTotal }}
                          </td>
                          <td>
                            {{ socioCompr.transacao[socioVendIdx].valorPago }}
                          </td>
                          <td>
                            {{ calculateValorPendente(socioCompr.transacao[socioVendIdx].valorTotal, socioCompr.transacao[socioVendIdx].valorPago) }}
                          </td>
                        </tr>
                      </template>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div class="col-sm-12 mt-3">
        <h4>Pendências</h4>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Pendência</th>
              <th>Responsável</th>
              <th>Data limite</th>
              <th>Ações</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pend, pendIdx) in pendencias" :key="pendIdx">
              <td>{{ pend.tarefa }}</td>
              <td>{{ pend.responsavel}}</td>
              <td>{{ pend.dataLimite.toLocaleDateString() }}</td>
              <td>
                <b-button size="sm" class="btn btn-gradient-primary" @click="openModalPendencia(pendIdx, pend)">Alterar</b-button>
                <b-button size="sm" class="btn btn-gradient-danger" @click="deletePendencia(pendIdx)"><i class="fa fa-trash fa-sm"></i></b-button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" @click="goStep2">Voltar</b-button>
        <b-button class="btn btn-gradient-primary" :to="{ name: 'Society', params: { msgSuccess: 'Evento salvo com sucesso' } }">Salvar</b-button>
      </div>
    </div>
    <template v-slot:modals>
      <b-modal ref="modalNovoSocio" :title="modalNovoSocio.title" @ok="actionModalNovoSocio" @cancel="closeModalNovoSocio" @close="closeModalNovoSocio">
        <b-form-group horizontal label="Nome" label-for="nome">
          <b-form-input type="text" id="nome" v-model="modalNovoSocio.nome"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Tipo" label-for="tipo">
          <b-form-select id="tipo" v-model="modalNovoSocio.tipo" :options="modalNovoSocio.tipoOpt"></b-form-select>
        </b-form-group>
        <b-form-group horizontal :label="modalNovoSocio.tipo === 'PJ' ? 'CNPJ' : 'CPF'" label-for="identificacao">
          <b-form-input type="text" id="identificacao" v-model="modalNovoSocio.identificacao"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="E-mail" label-for="email">
          <b-form-input type="email" id="email" v-model="modalNovoSocio.email"></b-form-input>
        </b-form-group>
      </b-modal>
      <b-modal ref="modalPendencia" scrollable :title="modalPendencia.title" @ok="actionModalPendencia" @cancel="closeModalPendencia" @close="closeModalPendencia">
        <b-form-group horizontal label="Tarefa" label-for="tarefa">
          <b-form-input type="text" id="tarefa" v-model="modalPendencia.pendenciaAlterada.tarefa"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Descrição" label-for="descricao">
          <b-form-textarea id="descricao" v-model="modalPendencia.pendenciaAlterada.descricao"></b-form-textarea>
        </b-form-group>
        <b-form-group horizontal label="Responsável" label-for="responsavel">
          <b-form-input type="text" id="responsavel" v-model="modalPendencia.pendenciaAlterada.responsavel"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Data limite" label-for="dataLimite">
          <b-form-input type="date" id="dataLimite" v-model="modalPendencia.pendenciaAlterada.dataLimiteStr"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Observações" label-for="observacoes">
          <b-form-textarea id="observacoes" v-model="modalPendencia.pendenciaAlterada.observacoes"></b-form-textarea>
        </b-form-group>
      </b-modal>
    </template>
  </event-base>
</template>
<script>
import EventBase from './EventBase'

var currentDate = new Date()

export default {
  components: {
    EventBase
  },
  data () {
    return {
      dataEvento: currentDate.toISOString().substring(0, 10),
      sociedade: {},
      socios: [],
      especiesClasses: [],
      especiesClassesSelecionadas: [],
      precoMedioAcao: 0,
      step: 0,
      pendencias: [],
      modalNovoSocio: {
        title: 'Adicionar subscritor',
        tipoOpt: ['PF', 'PJ'],
        tipo: '',
        identificacao: '',
        nome: '',
        email: ''
      },
      modalPendencia: {
        title: 'Editar pendência',
        pendenciaAlterada: {}
      }
    }
  },
  computed: {
    valoresVendedor () {
      let socios = []
      for (let socioVendIdx in this.socios) {
        let acoes = {}
        for (let especieClasse of this.especiesClassesSelecionadas) {
          acoes[especieClasse] = {
            qtd: 0
          }
        }
        let valores = {
          acoes: acoes,
          total: 0,
          pago: 0,
          pendente: 0
        }
        console.log(JSON.stringify(valores))
        if (this.socios[socioVendIdx].vendedor) {
          for (let socioComprIdx of this.socios) {
            if (socioComprIdx.transacao[socioVendIdx]) {
              let transacao = socioComprIdx.transacao[socioVendIdx]
              for (let especieClasse of this.especiesClassesSelecionadas) {
                if (transacao.acoes[especieClasse]) {
                  valores.acoes[especieClasse].qtd += transacao.acoes[especieClasse].qtd
                }
              }
              valores.total += transacao.valorTotal
              valores.pago += transacao.valorPago
            }
          }
          valores.pendente = valores.total - valores.pago
        }
        socios.push(valores)
      }
      console.log(JSON.stringify(socios))
      return socios
    }
  },
  beforeMount () {
    this.sociedade = this.$store.getters.sociedade
    this.especiesClasses = this.$store.getters.especiesClasses
  },
  mounted () {
    this.socios = this.loadSocios()
    this.precoMedioAcao = this.calculatePrecoMedioAcao()
    this.goStep1()
  },
  methods: {
    loadSocios () {
      let socios = []
      for (let socio of this.$store.getters.socios) {
        let clonedSocio = Object.assign({}, socio)
        clonedSocio.exists = true
        clonedSocio.vendedor = false
        clonedSocio.comprador = false
        clonedSocio.valores = Object.assign({}, socio.valores)
        clonedSocio.acoes = {}
        for (let especieClasse of this.$store.getters.especiesClasses) {
          if (socio.acoes[especieClasse]) {
            clonedSocio.acoes[especieClasse] = Object.assign({}, socio.acoes[especieClasse])
          } else {
            clonedSocio.acoes[especieClasse] = {
              qtd: 0
            }
          }
        }
        clonedSocio.transacao = {}
        socios.push(clonedSocio)
      }
      console.log(JSON.stringify(socios))
      return socios
    },
    calculatePrecoMedioAcao () {
      let preco = this.sociedade.capitalSocial.integralizado + this.sociedade.capitalSocial.integralizar + this.sociedade.reserva.integralizado + this.sociedade.reserva.integralizar
      let qtdTotalAcoes = 0
      let qtdAcoes = {}
      for (let especieClasse of this.$store.getters.especiesClasses) {
        qtdAcoes[especieClasse] = {
          qtd: 0
        }
        for (let socio of this.socios) {
          if (socio.acoes[especieClasse]) {
            qtdAcoes[especieClasse].qtd += socio.acoes[especieClasse].qtd
            qtdTotalAcoes += socio.acoes[especieClasse].qtd
          }
        }
      }
      console.log(JSON.stringify(qtdAcoes))
      let precoMedio = preco / qtdTotalAcoes
      console.log('PRECO MEDIO: ' + precoMedio)
      return precoMedio
    },
    calculateValorPendente (total, pago) {
      return total - pago
    },
    goStep1 () {
      this.step = 1
    },
    goStep2 () {
      // Criar elemento para associar compradores e vendedores
      for (let socioComprIdx in this.socios) {
        this.socios[socioComprIdx].transacao = {}
        let socioCompr = this.socios[socioComprIdx]
        if (socioCompr.comprador) {
          for (let socioVendIdx in this.socios) {
            let socioVend = this.socios[socioVendIdx]
            if (socioVend.vendedor && socioComprIdx !== socioVendIdx) {
              let acoes = {}
              for (let especieClasse of this.especiesClassesSelecionadas) {
                acoes[especieClasse] = {
                  qtd: 0
                }
              }
              this.socios[socioComprIdx].transacao = {...this.socios[socioComprIdx].transacao,
                [socioVendIdx]: {
                  acoes: acoes,
                  valorTotal: 0,
                  valorPago: 0
                }
              }
            }
          }
        }
      }
      console.log(JSON.stringify(this.socios))
      this.step = 2
    },
    goStep3 () {
      // Verificar se existem pendencias relacionadas a integralização
      let pendencias = []
      for (let socioCompr of this.socios) {
        for (let socioVendIdx in this.socios) {
          if (socioCompr.transacao[socioVendIdx]) {
            let transacao = socioCompr.transacao[socioVendIdx]
            let valorPendente = transacao.valorTotal - transacao.valorPago
            let socioVend = this.socios[socioVendIdx]
            if (valorPendente > 0) {
              pendencias.push({
                tarefa: `Pagar ${valorPendente} para ${socioVend.nome}`,
                responsavel: socioCompr.nome,
                dataLimite: this.addDate(this.dataEvento, 30)
              })
            }
          }
        }
      }
      this.pendencias = pendencias
      this.step = 3
    },
    openModalNovoSocio () {
      this.$refs['modalNovoSocio'].show()
    },
    actionModalNovoSocio () {
      let novoSocio = {
        nome: this.modalNovoSocio.nome,
        tipo: this.modalNovoSocio.tipo,
        identificacao: this.modalNovoSocio.identificacao,
        exists: false,
        vendedor: false,
        comprador: true,
        valores: {
          pagos: 0,
          pagar: 0,
          recebidos: 0,
          receber: 0
        },
        acoes: {},
        transacao: {}
      }
      for (let especieClasse of this.especiesClasses) {
        novoSocio.acoes[especieClasse] = {
          qtd: 0
        }
      }
      this.socios.push(novoSocio)
      this.closeModalNovoSocio()
    },
    closeModalNovoSocio () {
      this.modalNovoSocio.tipo = ''
      this.modalNovoSocio.identificacao = ''
      this.modalNovoSocio.nome = ''
      this.$refs['modalNovoSocio'].hide()
    },
    openModalPendencia (idx, pendencia) {
      this.modalPendencia.pendenciaIdx = idx
      this.modalPendencia.pendenciaAlterada = pendencia
      this.modalPendencia.pendenciaAlterada.dataLimiteStr = this.modalPendencia.pendenciaAlterada.dataLimite.toISOString().substring(0, 10)
      this.$refs['modalPendencia'].show()
    },
    actionModalPendencia () {
      this.pendencias[this.modalPendencia.pendenciaIdx] = this.modalPendencia.pendenciaAlterada
      this.closeModalPendencia()
    },
    closeModalPendencia () {
      this.modalPendencia.pendenciaIdx = -1
      this.modalPendencia.pendenciaAlterada = {}
      this.$refs['modalPendencia'].hide()
    },
    deletePendencia (pendIdx) {
      this.pendencias.splice(pendIdx, 1)
    },
    addDate (baseDate, days) {
      var result = new Date(baseDate)
      result.setDate(result.getDate() + days)
      return result
    }
  }
}
</script>
<style scoped>
table.step2 th, table.step2 td,
table.step3 th, table.step3 td {
  min-width: 150px;
}
table.step2 td, table.step3 td {
  vertical-align: top;
}
</style>



