<template>
  <div>
    <b-row>
      <b-col cols="12">
        <div class="card my-3">
          <div class="card-header bg-primary">
            Valores totais
          </div>
          <div class="card-body py-1">
            <table class="table table-striped table-sm">
              <thead class="thead-dark">
                <tr>
                  <th>Espécie/Classe</th>
                  <th>Quantidade</th>
                  <th>Valor</th>
                  <th>&nbsp;</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(val, key) in valorTotal" :key="key">
                  <td>{{ key }}</td>
                  <td>{{ val.qtd }}</td>
                  <td>{{ val.preco }}</td>
                  <td>&nbsp;</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </b-col>
    </b-row>

  </div>
</template>
<script>
export default {
  name: 'SocietyTotal',
  data () {
    return {}
  },
  computed: {
    valorTotal () {
      return this.$store.getters.totalValues
    },
    pessoas () {
      return this.$store.state.pessoas
    }
  }
}
</script>
<style scoped>
.card {
  border: 1px solid rgba(0,0,0,0.125) !important;
}
.bg-primary {
  color: #fff;
}
</style>



