<script lang="js">
import {
  Doughnut
} from 'vue-chartjs'

export default {
  extends: Doughnut,
  data () {
    return {
      datacollection: {
        labels: [
          'Search Engines',
          'Direct Click',
          'Bookmarks Click'
        ],
        datasets: [{
          data: [30, 30, 40],
          backgroundColor: [
            '#26c4f2',
            '#00cdb9',
            '#ff5d92'
          ],
          borderColor: [
            '#26c4f2',
            '#00cdb9',
            '#ff5d92'
          ]
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: true,
        animation: {
          animateScale: true,
          animateRotate: true
        },
        legend: false
      }
    }
  },
  mounted () {
    this.renderChart(this.datacollection, this.options)
  }
}
</script>

<style scoped lang="scss">
.traffic-chart {
}
</style>



