<template>
  <div>
    <slot name="alerts"></slot>
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h2>{{ societyName }}</h2>
            <div class="row">
              <div class="col-md-12">
                <h4>{{ eventType }}</h4>
                <!-- CONTEUDO DA TELA - INICIO -->
                  <slot></slot>
                <!-- CONTEUDO DA TELA - FIM -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <slot name="modals"></slot>
    <slot name="summary"></slot>
  </div>
</template>
<script>
export default {
  props: ['societyName', 'eventType']
}
</script>



