<template lang="html">

  <section class="wizard">
    <div class="page-header">
      <h3 class="page-title">
        Wizard
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item"><a href="#">Forms</a></li>
          <li class="breadcrumb-item active" aria-current="page">Wizard</li>
        </ol>
      </nav>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <div class="card-body">
            <vue-good-wizard :steps="steps" :onNext="nextClicked" :onBack="backClicked">
              <div slot="page1">
                <h4>Step 1</h4>
                <p>This is step 1</p>
              </div>
              <div slot="page2">
                <h4>Step 2</h4>
                <p>This is step 2</p>
              </div>
              <div slot="page3">
                <h4>Step 3</h4>
                <p>This is step 3</p>
              </div>
              <div slot="page4">
                <h4>Step 4</h4>
                <p>This is step 4</p>
              </div>
            </vue-good-wizard>
          </div>
        </div>
      </div>
    </div>
  </section>

</template>

<script lang="js">
import { GoodWizard } from 'vue-good-wizard'
export default {
  name: 'wizard',
  components: {
    'vue-good-wizard': GoodWizard
  },
  data () {
    return {
      steps: [
        {
          label: 'Select Items',
          slot: 'page1'
        },
        {
          label: 'Add Constraints',
          slot: 'page2'
        },
        {
          label: 'Review',
          slot: 'page3'
        },
        {
          label: 'Apply',
          slot: 'page4',
          options: {
            nextDisabled: true
          }
        }
      ]
    }
  },
  methods: {
    nextClicked (currentPage) {
      console.log('next clicked', currentPage)
      return true
    },
    backClicked (currentPage) {
      console.log('back clicked', currentPage)
      return true
    }
  }
}
</script>

<style scoped lang="scss">
.wizard {
}
</style>



