<template lang="html">

  <section class="notifications">
    <div class="page-header">
      <h3 class="page-title">
        Notifications
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item"><a href="#">Alerts</a></li>
          <li class="breadcrumb-item active" aria-current="page">Notifications</li>
        </ol>
      </nav>
    </div>
    <div class="row">
      <div class="col-12 grid-margin">
        <div class="card">
          <div class="card-body">
            <div class="row">
              <div class="col-md-4 col-sm-6 d-flex justify-content-center border-right">
                <div class="wrapper text-center">
                  <h4 class="card-title">Alerts Popups</h4>
                  <p class="card-description">A basic message</p>
                  <alert-default />
                </div>
              </div>
              <div class="col-md-4 col-sm-6 d-flex justify-content-center border-right">
                <div class="wrapper text-center">
                  <h4 class="card-title">Error Alert</h4>
                  <p class="card-description">Alert With Error Message</p>
                  <alert-error />
                </div>
              </div>
              <div class="col-md-4 col-sm-6 d-flex justify-content-center">
                <div class="wrapper text-center">
                  <h4 class="card-title">Alerts Prompt</h4>
                  <p class="card-description">Alert with prompt text</p>
                  <alert-prompt />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
     <div class="row">
      <div class="col-12 grid-margin">
        <div class="card">
          <div class="card-body">
            <div class="row">
              <div class="col-md-4 col-sm-6 d-flex justify-content-center border-right">
                <div class="wrapper text-center">
                  <h4 class="card-title">Custom HTML</h4>
                  <p class="card-description">Alert With Custom HTML</p>
                  <alert-custom-html />
                </div>
              </div>
              <div class="col-md-4 col-sm-6 d-flex justify-content-center border-right">
                <div class="wrapper text-center">
                  <h4 class="card-title">Animated Alert</h4>
                  <p class="card-description">Alert With Animations</p>
                  <alert-animated />
                </div>
              </div>
              <div class="col-md-4 col-sm-6 d-flex justify-content-center">
                <div class="wrapper text-center">
                  <h4 class="card-title">Alerts Content</h4>
                  <p class="card-description">Alert with Images</p>
                  <alert-with-image />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title">Snotify Notifications</h4>
            <simple-snotify />
          </div>
        </div>
      </div>
    </div>
  </section>

</template>

<script lang="js">
import alertDefault from '../../components/alerts/sweet-alert/alertDefault'
import alertError from '../../components/alerts/sweet-alert/alertError'
import alertPrompt from '../../components/alerts/sweet-alert/alertPrompt'
import alertCustomHtml from '../../components/alerts/sweet-alert/alertCustomHTML'
import alertAnimated from '../../components/alerts/sweet-alert/alertAnimated'
import alertWithImage from '../../components/alerts/sweet-alert/alertWithImage'
import simpleSnotify from '../../components/alerts/snotify/simpleSnotify'
export default {
  name: 'notifications',
  components: {
    alertDefault,
    alertError,
    alertPrompt,
    alertCustomHtml,
    alertAnimated,
    alertWithImage,
    simpleSnotify
  }
}
</script>

<style scoped lang="scss">
.notifications {
}
</style>



