import Vue from 'vue'
import Router from 'vue-router'

// Dashboard Components
import dashboard from '../views/dashboard'

import mainView from '../mainView'

//  Advanced Components
import carousel from '../views/advanced-elements/carousel'
import loader from '../views/advanced-elements/loader'
import notifications from '../views/advanced-elements/notifications'

//  Apps Components
import calendar from '../views/apps/calendar'
import email from '../views/apps/email'
import todo from '../views/apps/todo'

//  Basic Components
import accordion from '../views/basic-elements/accordion'
import alerts from '../views/basic-elements/alerts'
import badges from '../views/basic-elements/badges'
import breadcrumbs from '../views/basic-elements/breadcrumbs'
import buttons from '../views/basic-elements/buttons'
import dropdowns from '../views/basic-elements/dropdowns'
import modals from '../views/basic-elements/modals'
import paginations from '../views/basic-elements/paginations'
import progress from '../views/basic-elements/progress'
import tabs from '../views/basic-elements/tabs'
import tooltips from '../views/basic-elements/tooltips'
import typography from '../views/basic-elements/typography'

//  Chart Components
import chartjs from '../views/charts/chartjs'

//  Form Components
import advancedFormElements from '../views/forms/advanced-form-elements'
import basicFormElements from '../views/forms/basic-form-elements'
import wizard from '../views/forms/wizard'

//  Icons Components
import flagIcons from '../views/icons/flag-icons'
import fontawesome from '../views/icons/fontawesome'
import mdiIcons from '../views/icons/mdi-icons'
import simpleline from '../views/icons/simple-line'
import themify from '../views/icons/themify'

//  Sample Page Components
import invoice from '../views/samples/e-commerce/invoice'
import pricingTable from '../views/samples/e-commerce/pricing-table'

import error404 from '../views/samples/error-pages/error_404'
import error500 from '../views/samples/error-pages/error_500'

import blankPage from '../views/samples/general-pages/blank-page'
import FAQ from '../views/samples/general-pages/faq_1'
import FAQ_2 from '../views/samples/general-pages/faq_2'
import newsGrid from '../views/samples/general-pages/news-grid'
import portfolio from '../views/samples/general-pages/portfolio'
import profile from '../views/samples/general-pages/profile'
import searchResult from '../views/samples/general-pages/search-result'
import timeline from '../views/samples/general-pages/timeline'

import login from '../views/samples/user-pages/login'
import register from '../views/samples/user-pages/register'

//  Table Components
import basicTables from '../views/tables/basic-table'

// TESTE - FIGO
import Society from '../views/society-old/Society'
import SocietyInput from '../views/society-old/SocietyInput'
import SocietySummary from '../views/society-old/SocietySummary'
import EmissaoInput from '../views/society-old/EmissaoInput'
import EmissaoInput2 from '../views/society-old/EmissaoInput2'
import EmissaoConfirm from '../views/society-old/EmissaoConfirm'

import EmissaoInputNew from '../views/society/EmissaoInput'
import TransferenciaInput from '../views/society/TransferenciaInput'
import ReducaoInput from '../views/society/ReducaoInput'
import ConstituicaoInput from '../views/society/ConstituicaoInput'

// TESTES GERAIS VUE
import Customer from '../views/testes/Customer'
import TimelineHorizontal from '../views/testes/TimelineHorizontal'
import TimelineEvent from '../views/testes/TimelineEvent'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  mode: 'hash',
  routes: [{
    path: '/',
    redirect: '/society',
    component: mainView,
    children: [
      {
        path: '/dashboard',
        name: 'dashboard',
        component: dashboard
      },
      {
        path: '/carousel',
        component: carousel
      },
      {
        path: '/loaders',
        component: loader
      },
      {
        path: '/notifications',
        component: notifications
      },
      {
        path: '/calendar',
        component: calendar
      },
      {
        path: '/email',
        component: email
      },
      {
        path: '/todo',
        component: todo
      },
      {
        path: '/accordions',
        component: accordion
      },
      {
        path: '/alerts',
        component: alerts
      },
      {
        path: '/badges',
        component: badges
      },
      {
        path: '/breadcrumbs',
        component: breadcrumbs
      },
      {
        path: '/buttons',
        name: 'buttons',
        component: buttons
      },
      {
        path: '/dropdowns',
        component: dropdowns
      },
      {
        path: '/modals',
        component: modals
      },
      {
        path: '/paginations',
        component: paginations
      },
      {
        path: '/progress',
        component: progress
      },
      {
        path: '/tabs',
        component: tabs
      },
      {
        path: '/tooltips',
        component: tooltips
      },
      {
        path: '/typography',
        component: typography
      },
      {
        path: '/chartjs',
        component: chartjs
      },
      {
        path: '/advancedFormElements',
        component: advancedFormElements
      },
      {
        path: '/basicFormElements',
        component: basicFormElements
      },
      {
        path: '/wizard',
        component: wizard
      },
      {
        path: '/flagIcons',
        component: flagIcons
      },
      {
        path: '/fontawesome',
        component: fontawesome
      },
      {
        path: '/mdiIcons',
        component: mdiIcons
      },
      {
        path: '/simpleline',
        component: simpleline
      },
      {
        path: '/themifyIcons',
        component: themify
      },
      {
        path: '/invoice',
        component: invoice
      },
      {
        path: '/pricing-table',
        component: pricingTable
      },
      {
        path: '/blank_page',
        component: blankPage
      },
      {
        path: '/FAQ',
        component: FAQ
      },
      {
        path: '/FAQ_2',
        component: FAQ_2
      },
      {
        path: '/news_grid',
        component: newsGrid
      },
      {
        path: '/portfolio',
        component: portfolio
      },
      {
        path: '/profile',
        component: profile
      },
      {
        path: '/search_result',
        component: searchResult
      },
      {
        path: '/timeline',
        name: 'timeline',
        component: timeline
      },
      {
        path: '/basic_table',
        name: 'basicTables',
        component: basicTables
      },
      {
        path: '/society',
        name: 'Society',
        component: Society,
        props: true,
        children: [
          {
            path: '/society-summary/:id',
            name: 'SocietySummary',
            component: SocietySummary,
            props: true
          }
        ]
      },
      {
        path: '/society-input',
        name: 'SocietyInput',
        component: SocietyInput
      },
      {
        path: '/emissao-input',
        name: 'EmissaoInput',
        component: EmissaoInput
      },
      {
        path: '/emissao-input2',
        name: 'EmissaoInput2',
        component: EmissaoInput2
      },
      {
        path: '/emissao-confirm',
        name: 'EmissaoConfirm',
        component: EmissaoConfirm,
        props: true
      },
      {
        path: '/emissao-input-new',
        name: 'EmissaoInputNew',
        component: EmissaoInputNew
      },
      {
        path: '/transferencia-input',
        name: 'TransferenciaInput',
        component: TransferenciaInput
      },
      {
        path: '/reducao-input',
        name: 'ReducaoInput',
        component: ReducaoInput
      },
      {
        path: '/constituicao-input',
        name: 'ConstituicaoInput',
        component: ConstituicaoInput
      },
      {
        path: '/customer',
        name: 'customer',
        component: Customer
      },
      {
        path: '/timeline_horizontal',
        name: 'timelineHorizontal',
        component: TimelineHorizontal,
        children: [
          {
            path: '/timelineHorizontal/:id',
            name: 'timelineEvent',
            component: TimelineEvent,
            props: true
          }
        ]
      }
    ]
  },
  {
    path: '*',
    redirect: '/pages/error_404',
    component: {
      render (c) { return c('router-view') }
    },
    children: [
      {
        path: '/pages/error_404',
        component: error404
      },
      {
        path: '/pages/error_500',
        component: error500
      },
      {
        path: '/pages/login',
        component: login
      },
      {
        path: '/pages/register',
        component: register
      }
    ]}
  ]
})



