<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1>Horizontal Timeline with Swiper</h1>
        <!--
        <p>
          <a href="https://codepen.io/superpikar/pen/wrevRq">Fonte</a>
        </p>Credit :
        <ul>
          <li>
            Horizontal timeline CSS based on
            <a
              href="https://codepen.io/abhisharma2/pen/vEKWVo"
            >https://codepen.io/abhisharma2/pen/vEKWVo</a> with a few modifications.
          </li>
          <li>
            Website development step
            <a
              href="https://xbsoftware.com/blog/website-development-process-full-guide/"
            >https://xbsoftware.com/blog/website-development-process-full-guide/</a>
          </li>
          <li>
            Swiper Grab Cursor
            <a
              href="http://idangero.us/swiper/demos/12-grab-cursor.html"
            >http://idangero.us/swiper/demos/12-grab-cursor.html</a>
          </li>
        </ul>
        -->
        <div class="swiper-container">
          <div class="swiper-wrapper timeline">
            <div class="swiper-slide" v-for="(item, idx) in steps" :key="idx">
              <div class="timestamp">
                <span class="date">{{item.dateLabel}}</span>
              </div>
              <div class="row">
                <div class="col-6 left"></div>
                <div class="col-6 right"></div>
              </div>
              <div class="status">
                <span>
                  <router-link
                    :to="{ name: 'timelineEvent', params: { id: item.id, item: item }}"
                  >{{item.title}}</router-link>
                </span>
              </div>
            </div>
          </div>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>
<style scoped>
.swiper-slide .left,
.swiper-slide .right {
  border-top: 4px solid #573d4d;
}
.swiper-slide:first-of-type .left,
.swiper-slide:last-of-type .right {
  border-top: unset;
}
.container {
  padding: 50px 0;
}
.timeline {
  margin: 50px 0;
  list-style-type: none;
  display: flex;
  padding: 0;
  text-align: center;
}
.timeline li {
  transition: all 200ms ease-in;
}
.timestamp {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 100;
}
.status {
  padding: 0px 40px;
  display: flex;
  justify-content: center;
  /* border-top: 4px solid #573d4d; */
  position: relative;
  transition: all 200ms ease-in;
}
.status span {
  font-weight: 600;
  padding-top: 20px;
}
.status span:before {
  font-family: "FontAwesome";
  content: "\f005";
  color: lightblue;
  width: 30px;
  height: 30px;
  line-height: 25px;
  vertical-align: middle;
  text-align: center;
  background-color: #e8eeff;
  border-radius: 30px;
  border: 4px solid #573d4d;
  position: absolute;
  top: -15px;
  left: 42%;
  transition: all 200ms ease-in;
}
.swiper-slide-active .status span:before {
  color: #f39c12;
  background-color: #2ecc71;
  border: 4px solid #16a085;
}
.swiper-control {
  text-align: right;
}
.swiper-container {
  width: 100%;
  height: 250px;
  margin: 50px 0;
  overflow: hidden;
  padding: 0 20px 30px 20px;
}
.swiper-slide {
  width: 200px;
  text-align: center;
  font-size: 18px;
}
.swiper-slide:nth-child(2n) {
  width: 40%;
}
.swiper-slide:nth-child(3n) {
  width: 20%;
}
</style>
<script>
import Swiper from 'swiper'
// import 'swiper/css/swiper.css'
// import 'swiper/js/swiper.js'

const data = [
  { id: 1, dateLabel: '2017-10-20', title: 'Criação' },
  { id: 2, dateLabel: '2018-01-20', title: 'Emissão' },
  { id: 3, dateLabel: '2018-02-15', title: 'Emissão' },
  { id: 4, dateLabel: '2018-02-20', title: 'Integralização' },
  { id: 5, dateLabel: '2018-06-20', title: 'Transferência' },
  { id: 6, dateLabel: '2018-11-10', title: 'Redução' }
]

export default {
  data () {
    return {
      steps: data,
      swiper: null
    }
  },
  mounted () {
    this.swiper = new Swiper('.swiper-container', {
      // pagination: '.swiper-pagination',
      slidesPerView: 4,
      paginationClickable: true,
      freeMode: true,
      grabCursor: true,
      nextButton: '.next-slide',
      prevButton: '.prev-slide'
    })
  }
}
</script>



