<script lang="js">
import {
  Bar
} from 'vue-chartjs'

export default {
  extends: Bar,
  data () {
    return {
      datacollection: {
        labels: ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG'],
        datasets: [
          {
            label: 'CHN',
            borderColor: '#aa1eff',
            backgroundColor: '#aa1eff',
            hoverBackgroundColor: '#aa1eff',
            pointRadius: 0,
            borderWidth: 1,
            fill: 'origin',
            data: [20, 40, 15, 35, 25, 50, 30, 20]
          },
          {
            label: 'USA',
            borderColor: '#ff8790',
            backgroundColor: '#ff8790',
            hoverBackgroundColor: '#ff8790',
            pointRadius: 0,
            borderWidth: 1,
            fill: 'origin',
            data: [40, 30, 20, 10, 50, 15, 35, 40]
          },
          {
            label: 'UK',
            borderColor: '#20c5f2',
            backgroundColor: '#20c5f2',
            hoverBackgroundColor: '#20c5f2',
            pointRadius: 0,
            borderWidth: 1,
            fill: 'origin',
            data: [70, 10, 30, 40, 25, 50, 15, 30]
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: true,
        legend: false,
        scales: {
          yAxes: [{
            ticks: {
              display: false,
              min: 0,
              stepSize: 20,
              max: 80
            },
            gridLines: {
              drawBorder: false,
              color: 'rgba(235,237,242,1)',
              zeroLineColor: 'rgba(235,237,242,1)'
            }
          }],
          xAxes: [{
            gridLines: {
              display: false,
              drawBorder: false,
              color: 'rgba(0,0,0,1)',
              zeroLineColor: 'rgba(235,237,242,1)'
            },
            ticks: {
              padding: 20,
              fontColor: '#9c9fa6',
              autoSkip: true
            },
            categoryPercentage: 0.5,
            barPercentage: 0.5
          }]
        },
        elements: {
          point: {
            radius: 0
          }
        }
      }
    }
  },
  mounted () {
    this.renderChart(this.datacollection, this.options)
  }
}
</script>

<style scoped lang="scss">
.visit-sales-chart {
}
</style>



