<template>
  <div>
    <div class="row">
      <div class="col-md-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h2>{{nome}}</h2>
              <div class="row">
                <div class="col-md-6">
                  <b-form-group horizontal label="Data" label-for="dataEvento">
                    <b-form-input type="date" id="dataEvento" v-model="dataEv"></b-form-input>
                  </b-form-group>
                </div>
                <div class="col-md-6 text-right">
                  <b-button class="btn btn-gradient-primary" @click="openAddPessoa"><i class="fa fa-user"></i>&nbsp;Nova pessoa</b-button>
                </div>
            </div>
            <society-person></society-person>
            <society-total></society-total>
            <div class="row">
              <div class="col-md-12 text-right">
                <b-button class="btn btn-light" @click="cancel()">Cancelar</b-button>
                <b-button class="btn btn-success" @click="cancel()">Salvar</b-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <b-modal ref="novaPessoa" :title="'Nova pessoa'" @ok="save" @cancel="closeModal" @close="closeModal">
      <b-form-group horizontal label="Nome" label-for="nome">
        <b-form-input type="text" id="nome" v-model="modalNovaPessoa.nome"></b-form-input>
      </b-form-group>
    </b-modal>
  </div>
</template>
<script>
import SocietyPerson from './SocietyPerson'
import SocietyTotal from './SocietyTotal'

var currentDate = new Date()

export default {
  components: {
    SocietyPerson,
    SocietyTotal
  },
  data () {
    return {
      nome: 'Sociedade Teste SA',
      dataEv: currentDate.toISOString().substring(0, 10),
      modalNovaPessoa: {
        pIndex: -1,
        nome: ''
      }
    }
  },
  methods: {
    openEditPessoa (pIndex, nome) {
      this.modalNovaPessoa.pIndex = pIndex
      this.modalNovaPessoa.nome = nome
      this.$refs['novaPessoa'].show()
    },
    openAddPessoa () {
      this.$refs['novaPessoa'].show()
    },
    save () {
      if (this.modalNovaPessoa.pIndex < 0) {
        this.$store.dispatch('addPessoa', this.modalNovaPessoa.nome)
      } else {
        let payload = {
          pIndex: this.modalNovaPessoa.pIndex,
          nome: this.modalNovaPessoa.nome
        }
        this.$store.dispatch('updatePessoa', payload)
      }
      this.closeModal()
    },
    closeModal () {
      this.modalNovaPessoa.pIndex = -1
      this.modalNovaPessoa.nome = ''
      this.$refs['novaPessoa'].hide()
    },
    cancel () {
      this.$router.go(-1)
    }
  }
}
</script>



