<template>
  <event-base :societyName="societyName" :eventType="eventType">
    <div class="row">
      <div class="col-md-6">
        <b-form-group horizontal label="Data" label-for="dataEvento">
          <b-form-input type="date" id="dataEvento" v-model="dataEv"></b-form-input>
        </b-form-group>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4">
        <b-form-group horizontal label="Capital atual" label-for="capitalSocial">
          <b-form-input readonly type="number" id="capitalSocial" :value="capitalSocial.valor"></b-form-input>
        </b-form-group>
      </div>
      <div class="col-md-4">
        <b-form-group horizontal label="Aumento do capital" label-for="aumentoCapital">
          <b-form-input readonly type="number" id="aumentoCapital" :value="aumentoCapital.valor"></b-form-input>
        </b-form-group>
      </div>
      <div class="col-md-4">
        <b-form-group horizontal label="Capital final" label-for="capitalFinal">
          <b-form-input readonly type="number" id="capitalFinal" :value="capitalSocial.valor + aumentoCapital.valor"></b-form-input>
        </b-form-group>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4">
        <b-form-group horizontal label="Reserva atual" label-for="reservaAtual">
          <b-form-input readonly type="number" id="reservaAtual" :value="capitalSocial.reserva"></b-form-input>
        </b-form-group>
      </div>
      <div class="col-md-4">
        <b-form-group horizontal label="Aumento da reserva" label-for="aumentoReserva">
          <b-form-input readonly type="number" id="aumentoReserva" :value="aumentoCapital.reserva"></b-form-input>
        </b-form-group>
      </div>
      <div class="col-md-4">
        <b-form-group horizontal label="Reserva final" label-for="reservaFinal">
          <b-form-input readonly type="number" id="reservaFinal" :value="capitalSocial.reserva + aumentoCapital.reserva"></b-form-input>
        </b-form-group>
      </div>
    </div>
    <div class="row">
      <div class="col-12" style="overflow-x: auto; overflow-y: auto">
        <table class="table table-striped">
          <thead>
            <tr>
              <th rowspan="2">Subscritor</th>
              <th colspan="5" v-for="especieClasse in especiesClasses" :key="especieClasse">{{ especieClasse }}</th>
            </tr>
            <tr>
              <template v-for="especieClasse in especiesClasses">
                <th :key="especieClasse + '_qtd'">Qtd</th>
                <th :key="especieClasse + '_preco'">Preço</th>
                <th :key="especieClasse + '_precoInt'">Valor integralizado</th>
                <th :key="especieClasse + '_reserva'">Reserva</th>
                <th :key="especieClasse + '_reservaInt'">Reserva integralizada</th>
              </template>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pessoa, pessoaIdx) in pessoas" :key="pessoaIdx">
              <td>{{ pessoa.nome }}</td>
              <template v-for="especieClasse in especiesClasses">
                <td :key="especieClasse + '_qtd_' + pessoaIdx">{{ pessoa.valoresAdicionados[especieClasse] ? pessoa.valoresAdicionados[especieClasse].qtd : 0 }}</td>
                <td :key="especieClasse + '_preco_' + pessoaIdx">{{ pessoa.valoresAdicionados[especieClasse] ? pessoa.valoresAdicionados[especieClasse].preco : 0 }}</td>
                <td :key="especieClasse + '_precoInt_' + pessoaIdx ">{{ pessoa.valoresAdicionados[especieClasse] ? pessoa.valoresAdicionados[especieClasse].precoInt : 0 }}</td>
                <td :key="especieClasse + '_reserva_'+ pessoaIdx">{{ pessoa.valoresAdicionados[especieClasse] ? pessoa.valoresAdicionados[especieClasse].reserva : 0 }}</td>
                <td :key="especieClasse + '_reservaInt_' + pessoaIdx">{{ pessoa.valoresAdicionados[especieClasse] ? pessoa.valoresAdicionados[especieClasse].reservaInt : 0 }}</td>
              </template>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="row" v-if="pendencias.length > 0">
      <div class="col-md-12">
        <hr />
        <h3>Pendências</h3>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Pendência</th>
              <th>Responsável</th>
              <th>Data limite</th>
              <th>Ações</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pend, pendIdx) in pendencias" :key="pendIdx">
              <td>{{ pend.tarefa }}</td>
              <td>{{ pend.responsavel}}</td>
              <td>{{ pend.dataLimite.toLocaleDateString() }}</td>
              <td>
                <b-button size="sm" class="btn btn-gradient-primary" @click="openModalPendencia(pendIdx, pend)">Alterar</b-button>
                <b-button size="sm" class="btn btn-gradient-danger" @click="deletePendencia(pendIdx)"><i class="fa fa-trash fa-sm"></i></b-button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12 text-right">
        <b-button class="btn btn-light" @click="cancel()">Voltar</b-button>
        <b-button class="btn btn-success" :to="{ name: 'Society', params: { msgSuccess: 'Evento salvo com sucesso' } }">Salvar</b-button>
      </div>
    </div>
    <template v-slot:modals>
      <b-modal ref="modalPendencia" scrollable :title="modalPendencia.title" @ok="actionModalPendencia" @cancel="closeModalPendencia" @close="closeModalPendencia">
        <b-form-group horizontal label="Tarefa" label-for="tarefa">
          <b-form-input type="text" id="tarefa" v-model="modalPendencia.pendenciaAlterada.tarefa"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Descrição" label-for="descricao">
          <b-form-textarea id="descricao" v-model="modalPendencia.pendenciaAlterada.descricao"></b-form-textarea>
        </b-form-group>
        <b-form-group horizontal label="Responsável" label-for="responsavel">
          <b-form-input type="text" id="responsavel" v-model="modalPendencia.pendenciaAlterada.responsavel"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Data limite" label-for="dataLimite">
          <b-form-input type="date" id="dataLimite" v-model="modalPendencia.pendenciaAlterada.dataLimiteStr"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Observações" label-for="observacoes">
          <b-form-textarea id="observacoes" v-model="modalPendencia.pendenciaAlterada.observacoes"></b-form-textarea>
        </b-form-group>
      </b-modal>
    </template>
  </event-base>
</template>
<script>
import EventBase from './EventBase'

var currentDate = new Date()

export default {
  components: {
    EventBase
  },
  props: [ 'pessoasProps', 'capitalSocial', 'aumentoCapital' ],
  data () {
    return {
      societyName: 'Sociedade Teste SA',
      eventType: 'Emissão',
      dataEv: currentDate.toISOString().substring(0, 10),
      pessoas: [],
      especiesClasses: [],
      pendencias: [],
      modalPendencia: {
        title: 'Editar pendência',
        pendenciaAlterada: {}
      }
    }
  },
  computed: {

  },
  mounted () {
    // Percorrer lista de pessoas e deixar somente as que possuem valores adicionais diferente do inicial
    this.pessoas = []
    this.especiesClasses = []
    for (let pessoa of this.pessoasProps) {
      console.log(JSON.stringify(pessoa))
      let acoesEmitidas = {}
      for (let especieClasse in pessoa.valoresAdicionados) {
        let valorAdicionado = false
        if (pessoa.valoresAdicionados[especieClasse].qtd > 0) {
          valorAdicionado = true
        }
        let precoTotal = pessoa.valoresAdicionados[especieClasse].preco
        let precoInt = pessoa.valoresAdicionados[especieClasse].precoInt
        if (precoTotal > 0) {
          valorAdicionado = true
          if (precoInt < precoTotal) {
            this.pendencias.push({
              tarefa: `Integralizar ${precoTotal - precoInt}`,
              responsavel: pessoa.nome,
              dataLimite: this.addDate(this.dataEv, 30)
            })
          }
        }
        let reserva = pessoa.valoresAdicionados[especieClasse].reserva
        let reservaInt = pessoa.valoresAdicionados[especieClasse].reservaInt
        if (reserva > 0) {
          valorAdicionado = true
          if (reservaInt < reserva) {
            this.pendencias.push({
              tarefa: `Integralizar ${reserva - reservaInt} na reserva`,
              responsavel: pessoa.nome,
              dataLimite: this.addDate(this.dataEv, 30)
            })
          }
        }
        if (valorAdicionado) {
          acoesEmitidas[especieClasse] = pessoa.valoresAdicionados[especieClasse]
          if (!this.especiesClasses.includes(especieClasse)) {
            this.especiesClasses.push(especieClasse)
          }
        }
      }
      /// https://stackoverflow.com/questions/679915/how-do-i-test-for-an-empty-javascript-object
      if (Object.keys(acoesEmitidas).length > 0) {
        // Pessoa tem algum valor de acao alterada - Adiciona na lista
        let clonePessoa = Object.assign({}, pessoa)
        clonePessoa.valoresAdicionados = acoesEmitidas
        this.pessoas.push(clonePessoa)
      }
    }
    console.log(JSON.stringify(this.pessoas))
    console.log(JSON.stringify(this.pendencias))
  },
  methods: {
    cancel () {
      this.$router.go(-1)
    },
    deletePendencia (pendIdx) {
      this.pendencias.splice(pendIdx, 1)
    },
    openModalPendencia (idx, pendencia) {
      this.modalPendencia.pendenciaIdx = idx
      this.modalPendencia.pendenciaAlterada = pendencia
      this.modalPendencia.pendenciaAlterada.dataLimiteStr = this.modalPendencia.pendenciaAlterada.dataLimite.toISOString().substring(0, 10)
      this.$refs['modalPendencia'].show()
    },
    actionModalPendencia () {
      this.pendencias[this.modalPendencia.pendenciaIdx] = this.modalPendencia.pendenciaAlterada
      this.closeModalPendencia()
    },
    closeModalPendencia () {
      this.modalPendencia.pendenciaIdx = -1
      this.modalPendencia.pendenciaAlterada = {}
      this.$refs['modalPendencia'].hide()
    },
    addDate (baseDate, days) {
      var result = new Date(baseDate)
      result.setDate(result.getDate() + days)
      return result
    }
  }
}
</script>



