<template>
  <div>
    <b-alert show dismissible variant="success" v-if="msgSuccess">{{ msgSuccess }}</b-alert>
    <div class="row">
      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h2>{{nome}} <span class="badge badge-pill badge-light"><a href="#" @click.prevent="openInfo()" id="info-popover"><i class="fa fa-info"></i></a></span></h2>
            <b-popover ref="popoverInfo" target="info-popover" placement="rightbottom" class="info">
              <template v-slot:title>Informações</template>
              <dl class="row">
                <dt class="col-sm-3">Nome</dt>
                <dd class="col-sm-9">{{ sociedade.nome }}</dd>
                <dt class="col-sm-3">Tipo</dt>
                <dd class="col-sm-9">{{ sociedade.tipo }}</dd>
                <dt class="col-sm-3">CNPJ</dt>
                <dd class="col-sm-9">{{ sociedade.identificacao }}</dd>
                <dt class="col-sm-12">Endereço</dt>
                <dd class="col-sm-12">
                  {{ sociedade.endereco.logradouro }} - {{ sociedade.endereco.complemento }}<br>
                  {{ sociedade.endereco.bairro }} - {{ sociedade.endereco.cidade }} - {{ sociedade.endereco.estado }}<br>
                  CEP: {{ sociedade.endereco.cep }}
                </dd>
              </dl>
              <div class="row">
                <div class="col-12 text-center">
                  <b-button size="sm" @click="closeInfo()">Fechar</b-button>
                </div>
              </div>
            </b-popover>
            <div class="row mt-3">
              <div class="col-md-6">
                <div class="card bg-gradient-primary card-img-holder text-white">
                  <div class="card-body">
                    <img src="../../assets/images/dashboard/circle.svg" class="card-img-absolute" alt="circle-image"/>
                    <h4 class="font-weight-normal mb-2">Capital Social
                      <i class="mdi mdi-coin mdi-48px float-right"></i>
                    </h4>
                    <h3 class="mb-2">{{ capitalSocialTotal.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) }}</h3>
                  </div>
                  <ul class="list-group list-group-flush">
                    <li class="list-group-item">Integralizado: {{ capitalSocialIntegralizadoAsDecimal }}</li>
                    <li class="list-group-item">A integralizar: {{ capitalSocialIntegralizarAsDecimal }}</li>
                  </ul>
                </div>
              </div>
              <div class="col-md-6">
                <div class="card bg-gradient-info card-img-holder text-white">
                  <div class="card-body">
                    <img src="../../assets/images/dashboard/circle.svg" class="card-img-absolute" alt="circle-image"/>
                    <h4 class="font-weight-normal mb-2">Reserva de capital

                      <i class="mdi mdi-briefcase-outline mdi-48px float-right"></i>
                    </h4>
                    <h3 class="mb-2">{{ reservaTotal.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) }}</h3>
                  </div>
                  <ul class="list-group list-group-flush">
                    <li class="list-group-item">Integralizado: {{ reservaIntegralizadoAsDecimal }}</li>
                    <li class="list-group-item">A integralizar: {{ reservaIntegralizarAsDecimal }}</li>
                  </ul>
                </div>
              </div>
              <div class="col-md-12">
                <b-progress height="2rem" :max="capitalSocialTotal" show-value class="my-3">
                  <b-progress-bar variant="primary" :value="capitalSocial.integralizado" :title="`Capital social integralizado: ${capitalSocialIntegralizadoAsDecimal}`"></b-progress-bar>
                  <b-progress-bar variant="primary" :value="capitalSocial.integralizar" striped style="opacity: 0.75" :title="`Capital social a integralizar: ${capitalSocialIntegralizarAsDecimal}`"></b-progress-bar>
                  <b-progress-bar variant="info" :value="reserva.integralizado" :title="`Reserva integralizada: ${reservaIntegralizadoAsDecimal}`"></b-progress-bar>
                  <b-progress-bar variant="info" :value="reserva.integralizar" striped style="opacity: 0.75" :title="`Reserva a integralizar: ${reservaIntegralizarAsDecimal}`"></b-progress-bar>
                </b-progress>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12">
                <div class="swiper-container">
                  <div class="swiper-wrapper timeline">
                    <div class="swiper-slide" v-for="(evento, eventoIdx) in eventos" :key="eventoIdx">
                      <div class="timestamp">
                        <span class="date">{{ evento.dataEvento.toLocaleDateString() }}</span>
                      </div>
                      <div class="row">
                        <div class="col-6 left"></div>
                        <div class="col-6 right"></div>
                      </div>
                      <div class="status">
                        <span>
                          <!--
                          <router-link
                            :to="{ name: 'SocietySummary', params: { id: evento.id, eventoIdx: eventoIdx }}"
                          >{{evento.type}}</router-link>
                          -->
                          <a href="#" @click.prevent="loadEvent(eventoIdx)">{{evento.type}}</a>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
                <div>
                  <div class="row">
                    <!--
                    <div class="col-6">
                      <button class="btn btn-light btn-block">Ver detalhes</button>
                    </div>
                    -->
                    <div class="col-md-12 text-right">
                      <b-dropdown id="ddown1" text="Incluir evento" variant="gradient-primary">
                        <b-dropdown-item><router-link :to="{ name: 'EmissaoInputNew' }">Emissão</router-link></b-dropdown-item>
                        <b-dropdown-item><router-link :to="{ name: 'TransferenciaInput' }">Transferência</router-link></b-dropdown-item>
                        <b-dropdown-item><router-link :to="{ name: 'ReducaoInput' }">Redução</router-link></b-dropdown-item>
                        <b-dropdown-item>Integralização</b-dropdown-item>
                        <b-dropdown-divider></b-dropdown-divider>
                        <b-dropdown-item><router-link :to="{ name: 'ConstituicaoInput' }">Nova data</router-link></b-dropdown-item>
                      </b-dropdown>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <!-- <router-view></router-view> -->
            <div class="row">
              <div class="col-md-12">
                <h3>Evento: {{ sumario.evento.type }}</h3>
                <h3>Data: {{ sumario.evento.dataEvento.toLocaleDateString() }}</h3>
              </div>
            </div>
            <hr>
            <div class="row">
              <div class="col-md-12">
                <h3>Composição acionária e capital social após evento</h3>
                <table class="table table-striped">
                  <thead>
                    <tr>
                      <th>Nome do sócio/acionista</th>
                      <th>Tipo</th>
                      <th v-for="especieClasse in sumario.especiesClasses" :key="especieClasse">{{ especieClasse }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="socio in sumario.evento.socios" :key="socio.id">
                      <td>{{ socio.nome }}</td>
                      <td>{{ socio.tipo }}</td>
                      <td v-for="especieClasse in sumario.especiesClasses" :key="especieClasse">
                        {{ socio.acoes[especieClasse] ? socio.acoes[especieClasse].qtd : 0 }}
                      </td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colspan="2">TOTAL</td>
                      <td v-for="especieClasse in sumario.especiesClasses" :key="especieClasse">
                        {{ valorTotalSumario[especieClasse].qtd }}
                      </td>
                    </tr>
                  </tfoot>
                </table>
              </div>
            </div>
            <hr>
            <div class="row">
              <div class="col-md-12">
                <h3>Pendências</h3>
                <table class="table table-striped">
                  <thead>
                    <tr>
                      <th>Pendência</th>
                      <th>Responsável</th>
                      <th>Data limite</th>
                      <th>Situação</th>
                      <th>Ações</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="pend in pendencias" :key="pend.id">
                      <td>{{ pend.tarefa }}</td>
                      <td>{{ pend.responsavel}}</td>
                      <td>{{ pend.dataLimite.toLocaleDateString() }}</td>
                      <td>
                        <span v-if="pend.concluida" class="badge badge-success">CONCLUÍDA</span>
                        <span v-else-if="pendenciaAtrasada(pend.dataLimite)" class="badge badge-danger">ATRASADA</span>
                        <span v-else class="badge badge-warning">DENTRO DO PRAZO</span>
                      </td>
                      <td><button class="btn btn-gradient-primary btn-sm">Alterar</button></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Swiper from 'swiper'
/*
const data = [
  { id: 1, dataEvento: new Date(2017, 9, 20), title: 'Constituição' },
  { id: 2, dataEvento: new Date(2018, 0, 22), title: 'Emissão' },
  { id: 3, dataEvento: new Date(2019, 1, 14), title: 'Emissão' },
  { id: 4, dataEvento: new Date(2019, 1, 27), title: 'Integralização' },
  { id: 5, dataEvento: new Date(2019, 5, 2), title: 'Transferência' },
  { id: 6, dataEvento: new Date(2019, 10, 19), title: 'Redução' }
]
*/
export default {
  props: [ 'msgSuccess' ],
  data () {
    return {
      eventos: this.$store.getters.eventos,
      swiper: null,
      infoShow: false,
      nome: 'Sociedade Teste SA',
      sociedade: {},
      capitalSocial: {
        integralizado: 0,
        integralizar: 0
      },
      reserva: {
        integralizado: 0,
        integralizar: 0
      },
      pendencias: [],
      sumario: {}
    }
  },
  computed: {
    capitalSocialIntegralizadoAsDecimal () {
      return this.capitalSocial.integralizado.toLocaleString('pt-BR', { style: 'decimal' })
    },
    capitalSocialIntegralizarAsDecimal () {
      return this.capitalSocial.integralizar.toLocaleString('pt-BR', { style: 'decimal' })
    },
    reservaIntegralizadoAsDecimal () {
      return this.reserva.integralizado.toLocaleString('pt-BR', { style: 'decimal' })
    },
    reservaIntegralizarAsDecimal () {
      return this.reserva.integralizar.toLocaleString('pt-BR', { style: 'decimal' })
    },
    capitalSocialTotal () {
      return this.capitalSocial.integralizado + this.capitalSocial.integralizar
    },
    reservaTotal () {
      return this.reserva.integralizado + this.reserva.integralizar
    },
    valorTotalSumario () {
      let valorTotal = {}
      for (let especieClasse of this.sumario.especiesClasses) {
        valorTotal[especieClasse] = {
          qtd: 0
        }
      }
      for (let socio of this.sumario.evento.socios) {
        for (let especieClasse of this.sumario.especiesClasses) {
          if (socio.acoes[especieClasse]) {
            valorTotal[especieClasse].qtd += socio.acoes[especieClasse].qtd
          }
        }
      }
      console.log('Valor total do evento: ' + JSON.stringify(valorTotal))
      return valorTotal
    }
  },
  beforeMount () {
    // let lastEventIdx = this.$store.getters.eventos.length - 1
    this.eventos = this.$store.getters.eventos
    this.sociedade = Object.assign({}, this.$store.getters.sociedade)
    this.capitalSocial = this.sociedade.capitalSocial
    this.reserva = this.sociedade.reserva
    this.pendencias = Object.assign({}, this.$store.getters.pendencias)
    let lastEventIdx = this.eventos.length - 1
    let ultimoEvento = this.eventos[lastEventIdx]
    this.loadSummary(ultimoEvento)
  },
  mounted () {
    let lastEventIdx = this.eventos.length - 1
    this.swiper = new Swiper('.swiper-container', {
      // pagination: '.swiper-pagination',
      slidesPerView: 4,
      paginationClickable: true,
      freeMode: true,
      grabCursor: true,
      nextButton: '.next-slide',
      prevButton: '.prev-slide',
      initialSlide: lastEventIdx
    })
  },
  methods: {
    pendenciaAtrasada (limitDate) {
      let currentDate = new Date()
      if (currentDate.getTime() < limitDate.getTime()) {
        return false
      }
      return true
    },
    openInfo () {
      this.$refs.popoverInfo.$emit('open')
    },
    closeInfo () {
      this.$refs.popoverInfo.$emit('close')
    },
    loadEvent (eventoIdx) {
      let evento = this.eventos[eventoIdx]
      this.loadSummary(evento)
    },
    loadSummary (evento) {
      let especiesClasses = []
      for (let socio of evento.socios) {
        for (let especieClasse in socio.acoes) {
          if (!especiesClasses.includes(especieClasse)) {
            especiesClasses.push(especieClasse)
          }
        }
      }
      this.sumario = {
        evento: evento,
        especiesClasses: especiesClasses
      }
    }
  }
}
</script>
<style scoped>
.swiper-slide .left,
.swiper-slide .right {
  border-top: 4px solid #573d4d;
}
.swiper-slide:first-of-type .left,
.swiper-slide:last-of-type .right {
  border-top: unset;
}
.container {
  padding: 50px 0;
}
.timeline {
  margin: 50px 0;
  list-style-type: none;
  display: flex;
  padding: 0;
  text-align: center;
}
.timeline li {
  transition: all 200ms ease-in;
}
.timestamp {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 100;
}
.status {
  padding: 0px 40px;
  display: flex;
  justify-content: center;
  /* border-top: 4px solid #573d4d; */
  position: relative;
  transition: all 200ms ease-in;
}
.status span {
  font-weight: 600;
  padding-top: 20px;
}
.status span:before {
  font-family: "FontAwesome";
  content: "\f005";
  color: lightblue;
  width: 30px;
  height: 30px;
  line-height: 25px;
  vertical-align: middle;
  text-align: center;
  background-color: #e8eeff;
  border-radius: 30px;
  border: 4px solid #573d4d;
  position: absolute;
  top: -15px;
  left: 42%;
  transition: all 200ms ease-in;
}
/*
.swiper-slide-active .status span:before {
  color: #f39c12;
  background-color: #2ecc71;
  border: 4px solid #16a085;
}
*/
.swiper-control {
  text-align: right;
}
.swiper-container {
  width: 100%;
  height: 200px;
  overflow: hidden;
  padding: 20px 10px;
}
.swiper-slide {
  width: 200px;
  text-align: center;
  font-size: 18px;
}
.swiper-slide:nth-child(2n) {
  width: 40%;
}
.swiper-slide:nth-child(3n) {
  width: 20%;
}
.popover {
  max-width: 400px !important;
}
.card-body {
  padding: 1.0rem 2.5rem 0.5rem;
}
.list-group-item {
  padding-left: 2.5rem !important;
  background-color: unset;
}
</style>



