<template>
  <div>
    <div class="row">
      <div class="col-md-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body">
            <h2>{{nome}}</h2>
            <div class="row">
              <div class="col-md-6">
                <b-form-group horizontal label="Data" label-for="dataEvento">
                  <b-form-input type="date" id="dataEvento" v-model="dataEv"></b-form-input>
                </b-form-group>
              </div>
            </div>
            <div class="row">
              <div class="col-md-4">
                <b-form-group horizontal label="Capital atual" label-for="capitalSocial">
                  <b-form-input readonly type="number" id="capitalSocial" :value="capitalSocial.valor"></b-form-input>
                </b-form-group>
              </div>
              <div class="col-md-4">
                <b-form-group horizontal label="Aumento do capital" label-for="aumentoCapital">
                  <b-form-input type="number" id="aumentoCapital" v-model.number="aumentoCapital.valor"></b-form-input>
                </b-form-group>
              </div>
              <div class="col-md-4">
                <b-form-group horizontal label="Capital final" label-for="capitalFinal">
                  <b-form-input readonly type="number" id="capitalFinal" :value="capitalSocial.valor + aumentoCapital.valor"></b-form-input>
                </b-form-group>
              </div>
            </div>
            <div class="row">
              <div class="col-md-4">
                <b-form-group horizontal label="Reserva atual" label-for="reservaAtual">
                  <b-form-input readonly type="number" id="reservaAtual" :value="capitalSocial.reserva"></b-form-input>
                </b-form-group>
              </div>
              <div class="col-md-4">
                <b-form-group horizontal label="Aumento da reserva" label-for="aumentoReserva">
                  <b-form-input type="number" id="aumentoReserva" v-model.number="aumentoCapital.reserva"></b-form-input>
                </b-form-group>
              </div>
              <div class="col-md-4">
                <b-form-group horizontal label="Reserva final" label-for="reservaFinal">
                  <b-form-input readonly type="number" id="reservaFinal" :value="capitalSocial.reserva + aumentoCapital.reserva"></b-form-input>
                </b-form-group>
              </div>
            </div>

            <div class="row">
              <!-- ********** SECAO SUBSCRITORES ********* -->
              <div class="col-md-12">
                <h4>Subscritores</h4>
                <div class="card my-3" v-for="(pessoa, pessoaIdx) in pessoasComputed" :key="pessoa.id">
                  <div class="card-header bg-gradient-primary">
                    {{pessoaIdx + 1}}. {{ pessoa.nome }} (#{{ pessoa.id }})
                  </div>
                  <div class="card-body especie py-1">
                    <div class="row py-2" v-for="(especie, especieIdx) in especies" :key="especie">
                      <div class="col-2">{{ especie }}</div>
                      <div class="col-10 evento-form">
                        <div class="row">
                           <!-- MOSTRAR QUANTIDADE ATUAL E O INCREMENTO -->
                          <div class="col-3">
                            <b-form-group label="Qtde atual" label-size="sm" :label-for="generateInputId('qtdAtual', pessoaIdx, especieIdx)">
                              <b-form-input :id="generateInputId('qtdAtual', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="pessoa.valores[especie].qtd"></b-form-input>
                            </b-form-group>
                          </div>
                          <div class="col-3">
                            <b-form-group label="Qtde adicional" label-size="sm" :label-for="generateInputId('qtdAdic', pessoaIdx, especieIdx)">
                              <b-form-input type="number" :id="generateInputId('qtdAdic', pessoaIdx, especieIdx)" size="sm" v-model.number="pessoa.valoresAdicionados[especie].qtd"></b-form-input>
                            </b-form-group>
                          </div>
                          <div class="col-3 offset-3">
                            <b-form-group label="Qtde final" label-size="sm" :label-for="generateInputId('qtdFinal', pessoaIdx, especieIdx)">
                              <b-form-input :id="generateInputId('qtdFinal', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="calculateFinalValueInt(pessoa.valores[especie].qtd, pessoa.valoresAdicionados[especie].qtd)"></b-form-input>
                            </b-form-group>
                          </div>
                        </div>
                        <div class="row">
                          <!--
                            MOSTRAR VALOR ATUAL, VALOR A ADICIONAR E VALOR A INTEGRALIZAR
                            SE TIVER VALOR PARA INTEGRALIZAR, PREENCHER A DATA LIMITE
                          -->
                          <!--
                          <div class="col-3">
                            <b-form-group label="Valor atual" label-size="sm" :label-for="generateInputId('vlAtual', pessoaIdx, especieIdx)">
                              <b-form-input :id="generateInputId('vlAtual', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="pessoa.valores[especie].preco"></b-form-input>
                            </b-form-group>
                          </div>
                          -->
                          <div class="col-3 offset-3">
                            <b-form-group label="Custo aquisição" label-size="sm" :label-for="generateInputId('vlAdic', pessoaIdx, especieIdx)">
                              <b-input-group size="sm" prepend="$">
                                <b-form-input type="number" :id="generateInputId('vlAdic', pessoaIdx, especieIdx)" v-model.number="pessoa.valoresAdicionados[especie].preco"></b-form-input>
                              </b-input-group>
                            </b-form-group>
                          </div>
                          <div class="col-3">
                            <b-form-group label="Valor integralizado" label-size="sm" :label-for="generateInputId('vlAdicInt', pessoaIdx, especieIdx)">
                              <b-input-group size="sm" prepend="$">
                                <b-form-input type="number" :id="generateInputId('vlAdicInt', pessoaIdx, especieIdx)" v-model.number="pessoa.valoresAdicionados[especie].precoInt"></b-form-input>
                              </b-input-group>
                            </b-form-group>
                          </div>
                          <!--
                          <div class="col-3">
                            <b-form-group label="Valor final" label-size="sm" :label-for="generateInputId('vlFinal', pessoaIdx, especieIdx)">
                              <b-form-input :id="generateInputId('vlFinal', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="calculateFinalValueInt(pessoa.valores[especie].preco, pessoa.valoresAdicionados[especie].preco)"></b-form-input>
                            </b-form-group>
                          </div>
                           -->
                        </div>
                        <div class="row">
                          <!--
                            MOSTRAR VALOR ATUAL, VALOR A ADICIONAR E VALOR A INTEGRALIZAR
                            SE TIVER VALOR PARA INTEGRALIZAR, PREENCHER A DATA LIMITE
                          -->
                          <!--
                          <div class="col-3">
                            <b-form-group label="Reserva atual" label-size="sm" :label-for="generateInputId('vlResAtual', pessoaIdx, especieIdx)" >
                              <b-form-input :id="generateInputId('vlResAtual', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="pessoa.valores[especie].reserva"></b-form-input>
                            </b-form-group>
                          </div>
                          -->
                          <div class="col-3 offset-3">
                            <b-form-group label="Reserva adicional" label-size="sm" :label-for="generateInputId('vlResAdic', pessoaIdx, especieIdx)">
                              <b-input-group size="sm" prepend="$">
                                <b-form-input type="number" :id="generateInputId('vlResAdic', pessoaIdx, especieIdx)" v-model.number="pessoa.valoresAdicionados[especie].reserva"></b-form-input>
                              </b-input-group>
                            </b-form-group>
                          </div>
                          <div class="col-3">
                            <b-form-group label="Reserva integralizada" label-size="sm" :label-for="generateInputId('vlResAdicInt', pessoaIdx, especieIdx)">
                              <b-input-group size="sm" prepend="$">
                                <b-form-input type="number" :id="generateInputId('vlResAdicInt', pessoaIdx, especieIdx)" v-model.number="pessoa.valoresAdicionados[especie].reservaInt"></b-form-input>
                              </b-input-group>
                            </b-form-group>
                          </div>
                          <!--
                          <div class="col-3">
                            <b-form-group label="Reserva final" label-size="sm" :label-for="generateInputId('vlResFinal', pessoaIdx, especieIdx)" >
                              <b-form-input :id="generateInputId('vlResFinal', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="calculateFinalValueInt(pessoa.valores[especie].reserva, pessoa.valoresAdicionados[especie].reserva)"></b-form-input>
                            </b-form-group>
                          </div>
                          -->
                          <div class="col-3">
                            <b-form-group label="Valor total investido" label-size="sm" :label-for="generateInputId('vlTotal', pessoaIdx, especieIdx)">
                              <b-form-input :id="generateInputId('vlTotal', pessoaIdx, especieIdx)" size="sm" readonly plaintext :value="calculateFinalValueInt(pessoa.valoresAdicionados[especie].preco, pessoa.valoresAdicionados[especie].reserva)"></b-form-input>
                            </b-form-group>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div> <!-- ".card-body" -->
                  <div class="card-footer">
                    <div class="row">
                      <div class="col-12">
                        <div>Total: {{ acaoTotalPorPessoa[pessoaIdx].qtd }} => {{ acaoAdicionadaPorPessoa[pessoaIdx].qtd }}</div>
                        <div>Valor total: R$ {{ acaoTotalPorPessoa[pessoaIdx].preco + acaoTotalPorPessoa[pessoaIdx].reserva }} => {{ acaoAdicionadaPorPessoa[pessoaIdx].preco + acaoAdicionadaPorPessoa[pessoaIdx].reserva }}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-12 text-right">
                <b-button class="btn btn-light" @click="cancel()">Cancelar</b-button>
                <b-button class="btn btn-success" :to="{ name: 'EmissaoConfirm', params: { pessoasProps: pessoasComputed, capitalSocial: capitalSocial, aumentoCapital: aumentoCapital }}">Avançar</b-button>
              </div>
            </div>
          </div>  <!-- SECTION .card-body -->
        </div> <!-- SECTION .card -->
      </div>
    </div>

    <b-modal ref="modalEspecieClasse" :title="modalEspecieClasse.title" @ok="actionModalEspecieClasse" @cancel="closeModalEspecieClasse" @close="closeModalEspecieClasse">
      <b-form-group horizontal label="Espécie" label-for="especie">
        <b-form-select id="especie" v-model="modalEspecieClasse.especie" :options="modalEspecieClasse.especieOpt"></b-form-select>
      </b-form-group>
      <b-form-group horizontal label="Classe" label-for="classe">
        <b-form-input type="text" id="classe" v-model="modalEspecieClasse.classe"></b-form-input>
      </b-form-group>
    </b-modal>

    <b-modal ref="modalSubscritor" :title="modalSubscritor.title" @ok="actionModalSubscritor" @cancel="closeModalSubscritor" @close="closeModalSubscritor">
      <b-form-group horizontal label="Nome" label-for="nome">
        <b-form-input type="text" id="nome" v-model="modalSubscritor.nome"></b-form-input>
      </b-form-group>
      <b-form-group horizontal label="Tipo" label-for="tipo">
        <b-form-select id="tipo" v-model="modalSubscritor.tipo" :options="modalSubscritor.tipoOpt"></b-form-select>
      </b-form-group>
      <b-form-group horizontal label="Identificação" label-for="identificacao">
        <b-form-input type="text" id="identificacao" v-model="modalSubscritor.identificacao"></b-form-input>
      </b-form-group>
    </b-modal>

    <div class="summary" @mouseover="summary.isHovering = true" @mouseout="summary.isHovering = false">
      <div class="row">
        <div class="col-12">
          <h4>Ações emitidas</h4>
          <table class="table table-sm">
            <thead>
              <tr>
                <th>&nbsp;</th>
                <th>Quantidade</th>
                <!--
                <th>Valor (preço + reserva)</th>
                -->
              </tr>
            </thead>
            <tbody>
              <tr v-for="(acao, especie) in acoesEmitidas" :key="especie">
                <th>{{ especie }}</th>
                <td>{{ acao.qtd }}</td>
                <!--
                <td>{{ acao.preco + acao.reserva }}</td>
                -->
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="row">
        <div class="col-6">
          <b-button block size="sm" @click="openModalEspecieClasse">Nova espécie/classe</b-button>
        </div>
        <div class="col-6">
          <b-button block size="sm" @click="openModalSubscritor">Novo subscritor</b-button>
        </div>
      </div>
    </div>

  </div> <!-- root div -->
</template>
<script>
var currentDate = new Date()

export default {
  data () {
    return {
      nome: 'Sociedade Teste SA',
      dataEv: currentDate.toISOString().substring(0, 10),
      capitalSocial: {
        valor: 10000,
        reserva: 1000
      },
      aumentoCapital: {
        valor: 0,
        reserva: 0
      },
      pessoas: [],
      especies: [],
      // acoes: {},
      // acoesRestantes: {},
      emissaoPreenchida: false,
      modalEspecieClasse: {
        title: 'Adicionar nova espécie/classe',
        especieOpt: ['Pref', 'Ord'],
        especie: '',
        classe: ''
      },
      modalSubscritor: {
        title: 'Adicionar subscritor',
        tipoOpt: ['PF', 'PJ'],
        tipo: '',
        identificacao: '',
        nome: ''
      },
      summary: {
        isHovering: false,
        opacity: 0.8,
        defaultOpacity: 0.8
      }
    }
  },
  computed: {
    pessoasComputed () {
      return this.pessoas
    },
    valorTotal () {
      return this.$store.getters.totalValues
    },
    acaoTotalPorPessoa () {
      console.log('***** ACAO TOTAL POR PESSOA')
      let total = []
      for (let pessoa of this.pessoas) {
        let valTotal = {
          qtd: 0,
          preco: 0,
          precoInt: 0,
          reserva: 0,
          reservaInt: 0
        }
        for (let especie of this.especies) {
          let val = pessoa.valores[especie]
          valTotal.qtd += val.qtd
          valTotal.preco += val.preco
          valTotal.precoInt += val.precoInt
          valTotal.reserva += val.reserva
          valTotal.reservaInt += val.reservaInt
        }
        total.push(valTotal)
      }
      return total
    },
    acaoAdicionadaPorPessoa () {
      console.log('***** ACAO TOTAL POR PESSOA')
      let total = []
      for (let pessoa of this.pessoas) {
        let valTotal = {
          qtd: 0,
          preco: 0,
          precoInt: 0,
          reserva: 0,
          reservaInt: 0
        }
        for (let especie of this.especies) {
          let val = pessoa.valores[especie]
          let valAdicionado = pessoa.valoresAdicionados[especie]
          valTotal.qtd += val.qtd + valAdicionado.qtd
          valTotal.preco += val.preco + valAdicionado.preco
          valTotal.precoInt += val.precoInt + valAdicionado.precoInt
          valTotal.reserva += val.reserva + valAdicionado.reserva
          valTotal.reservaInt += val.reservaInt + valAdicionado.reservaInt
        }
        total.push(valTotal)
      }
      return total
    },
    /*
    acoesRestantes () {
      // Preenche as informações das ações emitidas
      let acoesRestantes = {}
      for (let especie in this.acoes) {
        acoesRestantes[especie] = Object.assign({}, this.acoes[especie])
      }
      for (let pessoa of this.pessoas) {
        for (let especie in this.acoes) {
          acoesRestantes[especie].qtd -= pessoa.valoresAdicionados[especie].qtd
          acoesRestantes[especie].preco -= pessoa.valoresAdicionados[especie].preco
        }
      }
      return acoesRestantes
    }, */
    acoesEmitidas () {
      let acoesEmitidas = {}
      for (let especie of this.especies) {
        acoesEmitidas[especie] = {
          qtd: 0,
          preco: 0,
          precoInt: 0,
          reserva: 0,
          reservaInt: 0
        }
      }
      for (let pessoa of this.pessoas) {
        console.log(`**** pessoa ${JSON.stringify(pessoa)}`)
        for (let especie in pessoa.valoresAdicionados) {
          acoesEmitidas[especie].qtd += pessoa.valoresAdicionados[especie].qtd
          acoesEmitidas[especie].preco += pessoa.valoresAdicionados[especie].preco
          acoesEmitidas[especie].precoInt += pessoa.valoresAdicionados[especie].precoInt
          acoesEmitidas[especie].reserva += pessoa.valoresAdicionados[especie].reserva
          acoesEmitidas[especie].reservaInt += pessoa.valoresAdicionados[especie].reservaInt
          // console.log(`***** ACOES EMITIDAS [${especie}]: ${JSON.stringify(acoesEmitidas[especie])}`)
        }
      }
      return acoesEmitidas
    }
  },
  methods: {
    generateInputId (prefix, pIdx, eIdx) {
      return prefix + '-' + pIdx + '-' + eIdx
    },
    calculateFinalValueInt (original, increment) {
      return parseInt(original) + parseInt(increment)
    },
    calculateFinalValueFloat (original, increment) {
      return parseFloat(original) + parseFloat(increment)
    },
    distributeValues () {
      this.emissaoPreenchida = !this.emissaoPreenchida
      if (this.emissaoPreenchida) {
        this.loadPersonValues() // <======== CARREGA AS PESSOAS DO STORE E AJUSTA PROPRIEDADES
        let qtdePessoas = this.pessoas.length
        for (let pessoa of this.pessoas) {
          for (let especie in this.acoes) {
            let qtdAcoes = Math.floor(this.acoes[especie].qtd / qtdePessoas)
            let precoAcoes = Math.floor(this.acoes[especie].preco / qtdePessoas)
            pessoa.valoresAdicionados[especie].qtd = qtdAcoes
            pessoa.valoresAdicionados[especie].preco = precoAcoes
          }
        }
      }
    },
    loadPersonValues () {
      let pessoasStore = this.$store.getters.pessoas
      for (let pessoa of pessoasStore) {
        console.log(`**** ORIGINAL: ${JSON.stringify(pessoa)}`)
        let clonePessoa = Object.assign({}, pessoa)
        clonePessoa.exists = true
        clonePessoa.valores = {}
        clonePessoa.valoresAdicionados = {}
        for (let especie of this.especies) {
          if (pessoa.valores[especie]) {
            clonePessoa.valores[especie] = Object.assign({}, pessoa.valores[especie])
          } else {
            clonePessoa.valores[especie] = {
              qtd: 0,
              preco: 0,
              precoInt: 0,
              reserva: 0,
              reservaInt: 0
            }
          }
          clonePessoa.valoresAdicionados[especie] = {
            qtd: 0,
            preco: 0,
            precoInt: 0,
            reserva: 0,
            reservaInt: 0
          }
        }
        console.log(`**** COPIADO: ${JSON.stringify(clonePessoa)}`)
        this.pessoas.push(clonePessoa)
      }
    },
    verifyClassAcoesRestantes (val) {
      if (val < 0) {
        return 'text-danger'
      } else if (val > 0) {
        return 'text-warning'
      }
      return 'text-success'
    },
    cancel () {
      this.$router.go(-1)
    },
    openModalEspecieClasse () {
      this.$refs['modalEspecieClasse'].show()
    },
    actionModalEspecieClasse () {
      let especieClasse = this.modalEspecieClasse.especie + ' ' + this.modalEspecieClasse.classe
      especieClasse = especieClasse.trim().replace(/\s\s+/g, ' ')
      if (!this.especies.includes(especieClasse)) {
        this.especies.push(especieClasse)
        console.log(JSON.stringify(this.especies))
        for (let pessoa of this.pessoas) {
          // https://vuex.vuejs.org/guide/mutations.html#mutations-follow-vue-s-reactivity-rules
          pessoa.valores = { ...pessoa.valores,
            [especieClasse]: {
              qtd: 0,
              preco: 0,
              precoInt: 0,
              reserva: 0,
              reservaInt: 0
            }
          }
          pessoa.valoresAdicionados = { ...pessoa.valoresAdicionados,
            [especieClasse]: {
              qtd: 0,
              preco: 0,
              precoInt: 0,
              reserva: 0,
              reservaInt: 0
            }
          }
          console.log(JSON.stringify(pessoa))
        }
      }
      this.closeModalEspecieClasse()
    },
    closeModalEspecieClasse () {
      this.modalEspecieClasse.especie = ''
      this.modalEspecieClasse.classe = ''
      this.$refs['modalEspecieClasse'].hide()
    },
    openModalSubscritor () {
      this.$refs['modalSubscritor'].show()
    },
    actionModalSubscritor () {
      let novaPessoa = {
        nome: this.modalSubscritor.nome,
        tipo: this.modalSubscritor.tipo,
        identificao: this.modalSubscritor.identificacao,
        exists: true,
        valores: {},
        valoresAdicionados: {}
      }
      for (let especie of this.especies) {
        novaPessoa.valores[especie] = {
          qtd: 0,
          preco: 0,
          precoInt: 0,
          reserva: 0,
          reservaInt: 0
        }
        novaPessoa.valoresAdicionados[especie] = {
          qtd: 0,
          preco: 0,
          precoInt: 0,
          reserva: 0,
          reservaInt: 0
        }
      }
      this.pessoas.push(novaPessoa)
      this.closeModalSubscritor()
    },
    closeModalSubscritor () {
      this.modalSubscritor.tipo = ''
      this.modalSubscritor.identificacao = ''
      this.modalSubscritor.nome = ''
      this.$refs['modalSubscritor'].hide()
    }
  },
  mounted () {
    this.especies = this.$store.getters.especiesAcoes
    /*
    for (let especie of this.especies) {
      this.acoes[especie] = {}
    }
    */
    this.loadPersonValues()
  }
}
</script>

<style scoped>
.evento-form label.col-form-label-sm {
    padding-bottom: 2px !important;
    margin-bottom: 0 !important;
}
.summary {
  position: fixed;
  right: 0;
  top: 100px;
  width: 30%;
  background-color: #fff;
  opacity: 0.75;
  box-shadow: 0px 3px 21px 0px rgba(0,0,0,0.2);
  transition: all 500ms ease-in-out
}
.summary:hover {
  opacity: 1;
}
.card-body.especie > .row {
  border-bottom: 1px solid #aaa;
}
.card-body.especie > .row:last-of-type {
  border-bottom: none;
}
</style>



