  getters: {
    sociedade: state => {
      return state.sociedade
    },
    socios: state => {
      return state.socios
    },
    eventos: state => {
      return state.eventos
    },
    especiesClasses: state => {
      let especies = []
      for (let socio of state.socios) {
        for (let especieClasse in socio.acoes) {
          if (!especies.includes(especieClasse)) {
            especies.push(especieClasse)
          }
        }
      }
      especies = especies.sort(function (a, b) {
        if (a === b) {
          return 0
        } else if (a === 'ON') {
          return -1
        } else if (b === 'ON') {
          return 1
        } else if (a > b) {
          return 1
        } else if (a < b) {
          return -1
        }
        return -1
      })
      return especies
    },
    pendencias: state => {
      return state.pendencias
    }
  },

  mutations: {
    addPendencia (state, pendencia) {
      state.pendencias.push(pendencia)
    }
  },

  actions: {
    addPendencia (context, pendencia) {
      context.commit('addPendencia', pendencia)
    }
  }

