<template>
  <div>
    <b-row>
      <b-col cols="12">
        <div class="card my-3" v-for="(pessoa, pIndex) in pessoas" :key="pessoa.id">
          <div class="card-header bg-gradient-primary">
            {{pIndex + 1}}. {{ pessoa.nome }} (#{{ pessoa.id }})
            <button type="button" class="btn btn-inverse-primary btn-rounded btn-icon" @click="deletePessoa(pIndex)">
              <i class="mdi mdi-delete-forever"></i>
            </button>
            <button type="button" class="btn btn-inverse-primary btn-rounded btn-icon" @click="editPessoa(pIndex, pessoa.nome)">
              <i class="mdi mdi-pencil"></i>
            </button>
          </div>
          <div class="card-body py-1">
            <table class="table table-striped table-sm">
              <thead class="thead-dark">
                <tr>
                  <th>Espécie/Classe</th>
                  <th>Quantidade</th>
                  <th>Valor</th>
                  <th>Ação</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(val, key) in pessoa.valores" :key="key">
                  <td>{{ key }}</td>
                  <td>{{ val.qtd }}</td>
                  <td>{{ val.preco }}</td>
                  <td>
                    <b-button size="sm" class="btn btn-sm btn-gradient-primary" @click="info(key, val, pIndex, 2, $event.target)">Alterar valor</b-button>
                    <b-button size="sm" class="btn btn-sm btn-gradient-danger" @click="deleteType(pIndex, key)"><i class="fa fa-trash fa-sm"></i></b-button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="card-footer text-right p-2">
            <b-button class="btn btn-sm btn-gradient-success" @click="info('', {}, pIndex, 1, $event.target)"><i class="fa fa-plus"></i>&nbsp;Nova espécie</b-button>
          </div>
        </div>

        <!--
        <div role="tablist" class="accordion accordion-solid-header">
          <b-card no-body v-for="pessoa in pessoas" :key="pessoa.id">
            <b-card-header header-tag="header" role="tab">
              <a href="#" v-b-toggle.accordion>{{ pessoa.nome }}</a>
            </b-card-header>
            <b-collapse id="accordion" accordion="my-accordion" role="tabpanel">
              <b-card-body>

              </b-card-body>
            </b-collapse>
          </b-card>
        </div>
        -->

      <!--
        <b-table striped :items="pessoas" :fields="fields">
          <template v-slot:cell(actions)="row">
            <b-button variant="primary" size="sm" class="btn btn-fw" @click="info(row.item, row.index, $event.target)">Mostrar info</b-button>
          </template>
        </b-table>
        -->
      </b-col>
    </b-row>
    <b-modal :id="infoModal.id" :title="infoModal.title" @ok="actionModal" @cancel="closeModal" @close="closeModal">
      <b-form-group horizontal label="Espécie/Classe" label-for="especie">
        <b-form-input type="text" id="especie" v-model="infoModal.tipo"></b-form-input>
      </b-form-group>
      <b-form-group horizontal label="Quantidade" label-for="qtd">
        <b-form-input type="number" id="qtd" v-model.number="infoModal.valor.qtd"></b-form-input>
      </b-form-group>
      <b-form-group horizontal label="Preço" label-for="preco">
        <b-form-input type="number" id="preco" v-model.number="infoModal.valor.preco"></b-form-input>
      </b-form-group>
    </b-modal>
  </div>
</template>
<script>
export default {
  name: 'SocietyPerson',
  data () {
    return {
      infoModal: {
        id: 'info-modal',
        pIndex: 0,
        action: 0,
        title: '',
        // content: '',
        tipo: '',
        valor: {}
      }
    }
  },
  computed: {
    pessoas () {
      return this.$store.getters.pessoas
    }
  },
  methods: {
    info (tipo, valor, pIndex, action, button) {
      this.infoModal.title = `Row index: pessoa[${pIndex}].valores[${tipo}]`
      // this.infoModal.content = JSON.stringify(tipo, null, 2)
      this.infoModal.action = action
      this.infoModal.pIndex = pIndex
      this.infoModal.tipo = tipo
      this.infoModal.valor = valor
      this.$root.$emit('bv::show::modal', this.infoModal.id, button)
    },
    editPessoa (pIndex, nome) {
      this.$parent.openEditPessoa(pIndex, nome)
    },
    deletePessoa (pIndex) {
      this.$store.dispatch('deletePessoa', pIndex)
    },
    closeModal () {
      this.$root.$emit('bv::hide::modal', this.infoModal.id)
      this.infoModal.action = 0
      this.infoModal.title = ''
      this.infoModal.tipo = ''
      this.infoModal.valor = 0
    },
    actionModal () {
      let updVal = {}
      updVal[this.infoModal.tipo] = this.infoModal.valor

      let payload = {
        pIndex: this.infoModal.pIndex,
        val: updVal
      }
      console.log(`ACTION: ${this.infoModal.action}, PESSOA_IDX: ${this.infoModal.pIndex}, TIPO: ${this.infoModal.tipo}, VALOR: ${this.infoModal.valor}`)
      if (this.infoModal.action === 1) { // Adicionar novo
        this.$store.dispatch('addNovoValor', payload)
      } else if (this.infoModal.action === 2) { // Atualizar valores
        this.$store.dispatch('updateValor', payload)
      }
      this.closeModal()
    },
    deleteType (pIndex, tipo) {
      let payload = {
        pIndex: pIndex,
        tipo: tipo
      }
      this.$store.dispatch('deleteValor', payload)
    }
  }
}
</script>
<style scoped>
.card {
  border: 1px solid rgba(0,0,0,0.125) !important;
}
.bg-gradient-primary {
  color: #fff;
}
</style>



