<template>
  <event-base :societyName="sociedade.nome" :eventType="'Emissão'">
    <div class="row" v-if="step === 1">
      <div class="col-md-12">
        <b-form-group label-cols-sm="2" label="Data" label-for="dataEvento">
          <b-form-input type="date" id="dataEvento" v-model="dataEvento" />
        </b-form-group>
      </div>
      <div class="col-md-12">
        <b-form-group label-cols-sm="2" label="Aumento de capital" label-for="aumentoCapital">
          <b-form-input type="number" id="aumentoCapital" number v-model="aumentoCapital" />
        </b-form-group>
      </div>
      <div class="col-md-12">
        <b-form-group label-cols-sm="2" label="Aumento de reserva" label-for="aumentoReserva">
          <b-form-input type="number" id="aumentoReserva" number v-model="aumentoReserva" />
        </b-form-group>
      </div>
    </div>

    <!-- ********** ETAPA 1: SELECIONAR SUBSCRITORES E ESPECIE DE AÇÕES A SEREM EMITIDAS ********** -->
    <div class="row" v-if="step === 1">
      <div class="col-sm-12 col-md-6">
        <h4>Selecione os subscritores</h4>
        <table class="table table-striped table-sm">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Nome</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(socio, socioIdx) in socios" :key="socioIdx">
              <td>
                <b-form-checkbox size="lg" v-model="socio.subscritor" />
              </td>
              <td>{{ socio.nome }}</td>
            </tr>
          </tbody>
        </table>
        <b-button class="btn btn-primary" ref="novoSocio" @click="openModalNovoSocio">Adicionar novo sócio/acionista</b-button>
        <!-- <p>{{ JSON.stringify(socios) }}</p> -->
      </div>
      <div class="col-sm-12 col-md-6">
        <h4>Selecione as espécies das ações</h4>
        <table class="table table-striped table-sm">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Espécie/Classe</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="especieClasse in especiesClasses" :key="especieClasse">
              <td>
                <b-form-checkbox size="lg" v-model="especiesClassesSelecionadas" :value="especieClasse" />
              </td>
              <td>{{ especieClasse }}</td>
            </tr>
          </tbody>
        </table>
        <b-button class="btn btn-primary" ref="novaEspecieClasse" @click="openModalEspecieClasse">Adicionar nova espécie/classe</b-button>
        <!-- <p>{{ JSON.stringify(especiesClassesSelecionadas) }}</p> -->
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" :to="{ name: 'Society' }">Cancelar</b-button>
        <b-button class="btn btn-gradient-primary" @click="goStep2">Preencher valores</b-button>
      </div>
    </div>

    <!-- ********** ETAPA 2: PREENCHER OS VALORES ********** -->
    <div class="row" v-if="step === 2">
      <div class="col-sm-12">
        <h4>Quantidade de ações a serem emitidas</h4>
        <div class="row">
          <div class="col-12" style="overflow-x: auto; overflow-y: auto">
            <table class="table table-striped step2">
              <thead>
                <tr>
                  <th>Subscritor</th>
                  <th v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">{{ especieClasse }}</th>
                  <!--
                  <th :key="especieClasse + '_capital'">Valor integralizado no CS</th>
                  <th :key="especieClasse + '_capitalInt'">Valor a integralizar no CS</th>
                  <th :key="especieClasse + '_reserva'">Reserva integralizada</th>
                  <th :key="especieClasse + '_reservaInt'">Reserva a integralizar</th>
                  -->
                  <th>Valor total no CS</th>
                  <th>Valor integralizado no CS</th>
                  <th>Reserva total</th>
                  <th>Reserva integralizada</th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(socio, socioIdx) in socios">
                  <tr v-if="socio.subscritor === true" :key="socioIdx">
                    <th>{{ socio.nome }}</th>
                    <td v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                      <b-form-group :description="'Qtde atual: ' + socio.acoes[especieClasse].qtd">
                        <b-form-input type="number" size="sm" number v-model="socio.acoesAdicionadas[especieClasse].qtd" />
                      </b-form-group>
                    </td>
                    <!--
                    <td :key="especieClasse + '_' + socioIdx + '_capital'">
                      <b-input type="number" v-model.number="socio.valoresAdicionados.capital" />
                    </td>
                    <td :key="especieClasse + '_' + socioIdx + '_capitalInt'">
                      <b-input type="number" v-model.number="socio.valoresAdicionados.capitalInt" />
                    </td>
                    <td :key="especieClasse + '_'+ socioIdx + '_reserva'">
                      <b-input type="number" v-model.number="socio.valoresAdicionados.reserva" />
                    </td>
                    <td :key="especieClasse + '_' + socioIdx + '_reservaInt'">
                      <b-input type="number" v-model.number="socio.valoresAdicionados.reservaInt" />
                    </td>
                    -->
                    <td>
                      <b-form-group>
                        <!-- VER https://bootstrap-vue.js.org/docs/components/form-input#v-model-modifiers -->
                        <b-form-input type="number" size="sm" number lazy v-model="socio.valoresAdicionados.capital" />
                      </b-form-group>
                    </td>
                    <td>
                      <b-form-group :description="'Valor pendente: ' + calculateValorPendente(socio)">
                        <b-form-input type="number" size="sm" number lazy v-model="socio.valoresAdicionados.capitalInt" />
                      </b-form-group>
                    </td>
                    <td>
                      <b-form-group>
                        <b-form-input type="number" size="sm" number lazy v-model="socio.valoresAdicionados.reserva" />
                      </b-form-group>
                    </td>
                    <td>
                      <b-form-group :description="'Reserva pendente: ' + calculateReservaPendente(socio)">
                        <b-form-input type="number" size="sm" number lazy v-model="socio.valoresAdicionados.reservaInt" />
                      </b-form-group>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" @click="goStep1">Voltar</b-button>
        <b-button class="btn btn-gradient-primary" @click="goStep3">Revisar valores</b-button>
      </div>
    </div>

    <!-- ********** ETAPA 3: REVISAR OS VALORES E SALVAR ********** -->
    <div class="row" v-if="step === 3">
      <div class="col-sm-12">
        <h4>Ações a serem emitidas</h4>
        <div class="row">
          <div class="col-12" style="overflow-x: auto; overflow-y: auto">
            <table class="table table-striped step3">
              <thead>
                <tr>
                  <th>Subscritor</th>
                  <th v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">{{ especieClasse }}</th>
                  <th>Valor total no CS</th>
                  <th>Valor integralizado no CS</th>
                  <th>Valor a integralizar</th>
                  <th>Reserva total</th>
                  <th>Reserva integralizada</th>
                  <th>Reserva a integralizar</th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(socio, socioIdx) in socios">
                  <tr v-if="socio.subscritor === true" :key="socioIdx">
                    <th>{{ socio.nome }}</th>
                    <td v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                      {{ socio.acoesAdicionadas[especieClasse].qtd }} <span class="badge badge-success">+ {{ calculateAumentoAcoes(socio, especieClasse) }}</span>
                    </td>
                    <td>{{ socio.valoresAdicionados.capital }}</td>
                    <td>{{ socio.valoresAdicionados.capitalInt }}</td>
                    <td>{{ calculateValorPendente(socio) }}</td>
                    <td>{{ socio.valoresAdicionados.reserva }}</td>
                    <td>{{ socio.valoresAdicionados.reservaInt }}</td>
                    <td>{{ calculateReservaPendente(socio) }}</td>
                  </tr>
                </template>
              </tbody>
              <tfoot>
                <tr>
                  <td>TOTAL</td>
                  <td v-for="especieClasse in especiesClassesSelecionadas" :key="especieClasse">
                    {{ acoesAdicionadas[especieClasse].qtd }}
                  </td>
                  <td>{{ valores.capital }}</td>
                  <td>{{ valores.capitalInt }}</td>
                  <td>{{ valores.capitalPend }}</td>
                  <td>{{ valores.reserva }}</td>
                  <td>{{ valores.reservaInt }}</td>
                  <td>{{ valores.reservaPend }}</td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
      <div class="col-sm-12">
        <h4>Aumento de capital</h4>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>&nbsp;</th>
              <th>Atual</th>
              <th>Aumento</th>
              <th>Final</th>
              <th>&nbsp;</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>Capital social integralizado</th>
              <td>{{ sociedade.capitalSocial.integralizado }}</td>
              <td>{{ valores.capitalInt }}</td>
              <td>
                {{ sociedade.capitalSocial.integralizado + valores.capitalInt }}
                <span v-if="valores.capitalInt > 0" class="badge badge-success">{{ calculatePercentage(sociedade.capitalSocial.integralizado, valores.capitalInt) }}</span>
              </td>
              <td rowspan="2">
                {{ sociedade.capitalSocial.integralizado + sociedade.capitalSocial.integralizar + valores.capital }}
                <span class="badge badge-success">{{ calculatePercentage(sociedade.capitalSocial.integralizado + sociedade.capitalSocial.integralizar, valores.capital) }}</span>
              </td>
            </tr>
            <tr>
              <th>Capital social a integralizar</th>
              <td>{{ sociedade.capitalSocial.integralizar }}</td>
              <td>{{ valores.capitalPend }}</td>
              <td>
                {{ sociedade.capitalSocial.integralizar + valores.capitalPend }}
                <span v-if="valores.capitalPend > 0" class="badge badge-success">{{ calculatePercentage(sociedade.capitalSocial.integralizar, valores.capitalPend) }}</span>
              </td>
            </tr>
            <tr>
              <th>Reserva integralizada</th>
              <td>{{ sociedade.reserva.integralizado }}</td>
              <td>{{ valores.reservaInt }}</td>
              <td>
                {{ sociedade.reserva.integralizado + valores.reservaInt }}
                <span v-if="valores.reservaInt > 0" class="badge badge-success">{{ calculatePercentage(sociedade.reserva.integralizado, valores.reservaInt) }}</span>
              </td>
              <td rowspan="2">
                {{ sociedade.reserva.integralizado + sociedade.reserva.integralizar + valores.reserva }}
                <span class="badge badge-success">{{ calculatePercentage(sociedade.reserva.integralizado + sociedade.reserva.integralizar, valores.reserva) }}</span>
              </td>
            </tr>
            <tr>
              <th>Reserva a integralizar</th>
              <td>{{ sociedade.reserva.integralizar }}</td>
              <td>{{ valores.reservaPend }}</td>
              <td>
                {{ sociedade.reserva.integralizar + valores.reservaPend }}
                <span v-if="valores.reservaPend > 0" class="badge badge-success">{{ calculatePercentage(sociedade.reserva.integralizar, valores.reservaPend) }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-sm-12 mt-3">
        <h4>Pendências</h4>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Pendência</th>
              <th>Responsável</th>
              <th>Data limite</th>
              <th>Ações</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(pend, pendIdx) in pendencias" :key="pendIdx">
              <td>{{ pend.tarefa }}</td>
              <td>{{ pend.responsavel}}</td>
              <td>{{ pend.dataLimite.toLocaleDateString() }}</td>
              <td>
                <b-button size="sm" class="btn btn-gradient-primary" @click="openModalPendencia(pendIdx, pend)">Alterar</b-button>
                <b-button size="sm" class="btn btn-gradient-danger" @click="deletePendencia(pendIdx)"><i class="fa fa-trash fa-sm"></i></b-button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-sm-12 col-md-12 my-3 text-center">
        <b-button class="btn btn-light" @click="goStep2">Voltar</b-button>
        <b-button class="btn btn-gradient-primary" :to="{ name: 'Society', params: { msgSuccess: 'Evento salvo com sucesso' } }">Salvar</b-button>
      </div>
    </div>
    <template v-slot:modals>
      <b-modal ref="modalNovoSocio" :title="modalNovoSocio.title" @ok="actionModalNovoSocio" @cancel="closeModalNovoSocio" @close="closeModalNovoSocio">
        <b-form-group horizontal label="Nome" label-for="nome">
          <b-form-input type="text" id="nome" v-model="modalNovoSocio.nome"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Tipo" label-for="tipo">
          <b-form-select id="tipo" v-model="modalNovoSocio.tipo" :options="modalNovoSocio.tipoOpt"></b-form-select>
        </b-form-group>
        <b-form-group horizontal :label="modalNovoSocio.tipo === 'PJ' ? 'CNPJ' : 'CPF'" label-for="identificacao">
          <b-form-input type="text" id="identificacao" v-model="modalNovoSocio.identificacao"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="E-mail" label-for="email">
          <b-form-input type="email" id="email" v-model="modalNovoSocio.email"></b-form-input>
        </b-form-group>
      </b-modal>
      <b-modal ref="modalEspecieClasse" :title="modalEspecieClasse.title" @ok="actionModalEspecieClasse" @cancel="closeModalEspecieClasse" @close="closeModalEspecieClasse">
        <b-form-group horizontal label="Espécie" label-for="especie">
          <b-form-select id="especie" v-model="modalEspecieClasse.especie" :options="modalEspecieClasse.especieOpt"></b-form-select>
        </b-form-group>
        <b-form-group horizontal label="Classe" label-for="classe">
          <b-form-input type="text" id="classe" v-model="modalEspecieClasse.classe"></b-form-input>
        </b-form-group>
      </b-modal>
      <b-modal ref="modalPendencia" scrollable :title="modalPendencia.title" @ok="actionModalPendencia" @cancel="closeModalPendencia" @close="closeModalPendencia">
        <b-form-group horizontal label="Tarefa" label-for="tarefa">
          <b-form-input type="text" id="tarefa" v-model="modalPendencia.pendenciaAlterada.tarefa"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Descrição" label-for="descricao">
          <b-form-textarea id="descricao" v-model="modalPendencia.pendenciaAlterada.descricao"></b-form-textarea>
        </b-form-group>
        <b-form-group horizontal label="Responsável" label-for="responsavel">
          <b-form-input type="text" id="responsavel" v-model="modalPendencia.pendenciaAlterada.responsavel"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Data limite" label-for="dataLimite">
          <b-form-input type="date" id="dataLimite" v-model="modalPendencia.pendenciaAlterada.dataLimiteStr"></b-form-input>
        </b-form-group>
        <b-form-group horizontal label="Observações" label-for="observacoes">
          <b-form-textarea id="observacoes" v-model="modalPendencia.pendenciaAlterada.observacoes"></b-form-textarea>
        </b-form-group>
      </b-modal>
    </template>
  </event-base>
</template>
<script>
import EventBase from './EventBase'

var currentDate = new Date()

export default {
  components: {
    EventBase
  },
  data () {
    return {
      dataEvento: currentDate.toISOString().substring(0, 10),
      aumentoCapital: 0,
      aumentoReserva: 0,
      sociedade: {},
      socios: [],
      especiesClasses: [],
      especiesClassesSelecionadas: [],
      step: 0,
      pendencias: [],
      modalNovoSocio: {
        title: 'Adicionar subscritor',
        tipoOpt: ['PF', 'PJ'],
        tipo: '',
        identificacao: '',
        nome: '',
        email: ''
      },
      modalEspecieClasse: {
        title: 'Adicionar nova espécie/classe',
        especieOpt: ['PN - Preferêncial', 'ON - Ordinária'],
        especie: '',
        classe: ''
      },
      modalPendencia: {
        title: 'Editar pendência',
        pendenciaAlterada: {}
      }
    }
  },
  computed: {
    acoesAdicionadas () {
      let acoes = {}
      for (let especieClasse of this.especiesClassesSelecionadas) {
        acoes[especieClasse] = {
          qtd: 0
        }
        for (let socio of this.socios) {
          acoes[especieClasse].qtd += socio.acoesAdicionadas[especieClasse].qtd
        }
      }
      return acoes
    },
    valores () {
      let valores = {
        capital: 0,
        capitalInt: 0,
        capitalPend: 0,
        reserva: 0,
        reservaInt: 0,
        reservaPend: 0
      }
      for (let socio of this.socios) {
        valores.capital += socio.valoresAdicionados.capital
        valores.capitalInt += socio.valoresAdicionados.capitalInt
        valores.reserva += socio.valoresAdicionados.reserva
        valores.reservaInt += socio.valoresAdicionados.reservaInt
      }
      valores.capitalPend = valores.capital - valores.capitalInt
      valores.reservaPend = valores.reserva - valores.reservaInt
      return valores
    }
  },
  mounted () {
    this.sociedade = this.$store.getters.sociedade
    this.especiesClasses = this.$store.getters.especiesClasses
    this.socios = this.loadSocios()
    this.goStep1()
  },
  methods: {
    loadSocios () {
      let socios = []
      for (let socio of this.$store.getters.socios) {
        let clonedSocio = Object.assign({}, socio)
        clonedSocio.exists = true
        clonedSocio.subscritor = false
        clonedSocio.valores = Object.assign({}, socio.valores)
        clonedSocio.valoresAdicionados = {
          capital: 0,
          capitalInt: 0,
          reserva: 0,
          reservaInt: 0
        }
        clonedSocio.acoes = {}
        clonedSocio.acoesAdicionadas = {}
        for (let especieClasse of this.$store.getters.especiesClasses) {
          if (socio.acoes[especieClasse]) {
            clonedSocio.acoes[especieClasse] = Object.assign({}, socio.acoes[especieClasse])
          } else {
            clonedSocio.acoes[especieClasse] = {
              qtd: 0
            }
          }
          clonedSocio.acoesAdicionadas[especieClasse] = {
            qtd: 0
          }
        }
        socios.push(clonedSocio)
      }
      console.log(JSON.stringify(socios))
      return socios
    },
    calculateValorPendente (socio) {
      return socio.valoresAdicionados.capital - socio.valoresAdicionados.capitalInt
    },
    calculateReservaPendente (socio) {
      return socio.valoresAdicionados.reserva - socio.valoresAdicionados.reservaInt
    },
    calculateAumentoAcoes (socio, especieClasse) {
      if (socio.acoes[especieClasse].qtd > 0) {
        return (socio.acoesAdicionadas[especieClasse].qtd / socio.acoes[especieClasse].qtd).toLocaleString('pt-BR', { style: 'percent', maximumFractionDigits: 1, minimumFractionDigits: 1 })
      }
      let value = 1
      return value.toLocaleString('pt-BR', { style: 'percent', maximumFractionDigits: 1, minimumFractionDigits: 1 })
    },
    goStep1 () {
      this.step = 1
    },
    goStep2 () {
      // Validar se existe pelo menos 1 subscritor e 1 especie de ação
      this.step = 2
    },
    goStep3 () {
      // Verificar se existem pendencias relacionadas a integralização
      let pendencias = []
      for (let socio of this.socios) {
        let capitalPend = socio.valoresAdicionados.capital - socio.valoresAdicionados.capitalInt
        if (capitalPend > 0) {
          pendencias.push({
            tarefa: `Integralizar ${capitalPend}`,
            responsavel: socio.nome,
            dataLimite: this.addDate(this.dataEvento, 30)
          })
        }
        let reservaPend = socio.valoresAdicionados.reserva - socio.valoresAdicionados.reservaInt
        if (reservaPend > 0) {
          pendencias.push({
            tarefa: `Integralizar ${reservaPend} na reserva`,
            responsavel: socio.nome,
            dataLimite: this.addDate(this.dataEvento, 30)
          })
        }
      }
      this.pendencias = pendencias
      this.step = 3
    },
    openModalEspecieClasse () {
      this.$refs['modalEspecieClasse'].show()
    },
    actionModalEspecieClasse () {
      let especieClasse = ''
      if (this.modalEspecieClasse.especie && this.modalEspecieClasse.especie.startsWith('ON')) {
        if (this.modalEspecieClasse.classe && this.modalEspecieClasse.classe.trim().length > 0) {
          especieClasse = 'ON' + this.modalEspecieClasse.classe.trim().toUpperCase()
        } else {
          especieClasse = 'ON'
        }
      } else if (this.modalEspecieClasse.especie && this.modalEspecieClasse.especie.startsWith('PN')) {
        if (this.modalEspecieClasse.classe && this.modalEspecieClasse.classe.trim().length > 0) {
          especieClasse = 'PN' + this.modalEspecieClasse.classe.trim().toUpperCase()
        } else {
          especieClasse = 'PN'
        }
      }
      // especieClasse = especieClasse.trim().replace(/\s\s+/g, ' ')
      if (!this.especiesClasses.includes(especieClasse)) {
        this.especiesClasses.push(especieClasse)
        this.especiesClassesSelecionadas.push(especieClasse)
        console.log(JSON.stringify(this.especiesClasses))
        for (let socio of this.socios) {
          // https://vuex.vuejs.org/guide/mutations.html#mutations-follow-vue-s-reactivity-rules
          socio.acoes = { ...socio.acoes,
            [especieClasse]: {
              qtd: 0
            }
          }
          socio.acoesAdicionadas = { ...socio.acoesAdicionadas,
            [especieClasse]: {
              qtd: 0
            }
          }
          console.log(JSON.stringify(socio))
        }
      }
      this.closeModalEspecieClasse()
    },
    closeModalEspecieClasse () {
      this.modalEspecieClasse.especie = ''
      this.modalEspecieClasse.classe = ''
      this.$refs['modalEspecieClasse'].hide()
    },
    openModalNovoSocio () {
      this.$refs['modalNovoSocio'].show()
    },
    actionModalNovoSocio () {
      let novoSocio = {
        nome: this.modalNovoSocio.nome,
        tipo: this.modalNovoSocio.tipo,
        identificacao: this.modalNovoSocio.identificacao,
        subscritor: true,
        exists: false,
        valores: {
          pagos: 0,
          pagar: 0,
          pagosReserva: 0,
          pagarReserva: 0,
          pagosTerceiros: 0,
          pagarTerceiros: 0,
          recebidos: 0,
          receber: 0,
          recebidosTerceiros: 0,
          receberTerceiros: 0
        },
        valoresAdicionados: {
          capital: 0,
          capitalInt: 0,
          reserva: 0,
          reservaInt: 0
        },
        acoes: {},
        acoesAdicionadas: {}
      }
      for (let especieClasse of this.especiesClasses) {
        novoSocio.acoes[especieClasse] = {
          qtd: 0
        }
        novoSocio.acoesAdicionadas[especieClasse] = {
          qtd: 0
        }
      }
      this.socios.push(novoSocio)
      this.closeModalNovoSocio()
    },
    closeModalNovoSocio () {
      this.modalNovoSocio.tipo = ''
      this.modalNovoSocio.identificacao = ''
      this.modalNovoSocio.nome = ''
      this.$refs['modalNovoSocio'].hide()
    },
    openModalPendencia (idx, pendencia) {
      this.modalPendencia.pendenciaIdx = idx
      this.modalPendencia.pendenciaAlterada = pendencia
      this.modalPendencia.pendenciaAlterada.dataLimiteStr = this.modalPendencia.pendenciaAlterada.dataLimite.toISOString().substring(0, 10)
      this.$refs['modalPendencia'].show()
    },
    actionModalPendencia () {
      this.pendencias[this.modalPendencia.pendenciaIdx] = this.modalPendencia.pendenciaAlterada
      this.closeModalPendencia()
    },
    closeModalPendencia () {
      this.modalPendencia.pendenciaIdx = -1
      this.modalPendencia.pendenciaAlterada = {}
      this.$refs['modalPendencia'].hide()
    },
    deletePendencia (pendIdx) {
      this.pendencias.splice(pendIdx, 1)
    },
    addDate (baseDate, days) {
      var result = new Date(baseDate)
      result.setDate(result.getDate() + days)
      return result
    },
    calculatePercentage (current, increment) {
      let value = increment / current
      return value.toLocaleString('pt-BR', { style: 'percent', maximumFractionDigits: 1, minimumFractionDigits: 1 })
    }
  }
}
</script>
<style scoped>
table.step2 th, table.step2 td,
table.step3 th, table.step3 td {
  min-width: 150px;
}
table.step2 td, table.step3 td {
  vertical-align: top;
}
</style>



